<template>
  <v-layout>
    <v-flex class="pa-1 ma-1 xs9 text-xs-center">
      <v-select
        v-model="fw"
        :items="firmwares"
        item-text="label"
        item-value="url"
        @change="download"
        label="Select a Firmware">
      </v-select>
      <v-progress-circular  v-if="pending" :indeterminate="true">
      </v-progress-circular>
      <span v-if="pending">
        <p>downloading data ...</p>
      </span>
    </v-flex>
  </v-layout>
</template>

<script>
import config from '../../config'

export default {
  name: 'DownloadFlash',
  data: function () {
    return {
      firmwares: config.FIRMWARES,
      pending: false,
      fw: null
    }
  },
  computed: {
    localFW () {
      return this.$store.state.localFW
    }
  },
  methods: {
    download: function (e) {
      this.pending = true
      let url = config.API_LOCATION + 'dl_flash?url=' + this.fw
      this.$http.get(url).then((response) => {
        this.pending = false
        this.$store.commit('setLocalFirmware', response.data.filename)
      }).catch(e => {
        this.pending = false
      })
    }
  }
}
</script>

