<template>
  <v-container  fill-height justify-center  justify-start  fluid grid-list-sm>
    <v-layout align-start wrap>
      <v-flex xs12>
        <v-card class="white">
          <!--
          <v-card-title class="black">
            {{application.name}} <br>
             update version {{application.__v}}
          </v-card-title>
          -->
          <v-card-text>
            <h2 class='pa-2 mt-2 primary'>Decode</h2>
            <codemirror
            class="code"
              @change="decodeChange"
              ref='editor'
              v-if='codec.decode'
              :value="codec.decode">
            </codemirror>

            <h2 class='pa-2 mt-2 primary'>Encode</h2>
            <codemirror
            class="code"
              @change="encodeChange"
              ref='editor'
              v-if='codec.encode'
              :value="codec.encode">
            </codemirror>
          </v-card-text>
          <v-btn
            absolute
            fab
            bottom
            right
            color="primary"
            @click.native="saveApplication()">
              <v-icon>done</v-icon>
          </v-btn>
      </v-card>
    </v-flex>
  </v-layout>
</v-container>
</template>

<script>
import Events from '../../store/event-api.js'
import EventBus from '../../event-bus.js'
import Persistent from '../../store/persistent'

require('codemirror/mode/javascript/javascript')
require('codemirror/mode/vue/vue')

require('codemirror/addon/hint/show-hint.js')
require('codemirror/addon/hint/show-hint.css')
require('codemirror/addon/hint/javascript-hint.js')

const DEFAULT_DECODE = `function (topic, payload) {
  const obj = {}
  object.raw = payload // apply your codec here
  return obj
}`

const DEFAULT_ENCODE = `function (topic, data) {
  const raw = atob(data) // apply your codec here
  return raw
}`

const DEFAULT_CODEC = {decode: DEFAULT_DECODE, encode: DEFAULT_ENCODE}

export default {
  name: 'ApplicationCodec',
  components: {
  },
  data: function () {
    return {
      application: {},
      codec: DEFAULT_CODEC,
      output: {},
      version: null
    }
  },
  created () {
    this.loadApplication(this.$route.params.id)
  },
  mounted () {
    EventBus.$emit('set-title', 'Application codec')
  },
  methods: {
    encodeChange: function (c) {
      const output = {
        encode: c,
        decode: this.output.decode
      }
      this.output = output
    },
    decodeChange: function (c) {
      const output = {
        decode: c,
        encode: this.output.encode
      }
      this.output = output
    },
    saveApplication: function () { // rename to save codec
      let url = Persistent.getters.baseUrl + '/api/v2/application/' + this.$route.params.id + '/codec'
      // prevent update if no changes
      if (JSON.stringify(this.output) === JSON.stringify(this.application.codec)) {
        this.$router.push({
          name: 'ApplicationSettings',
          params: { id: this.$route.params.id }
        })
        return
      }
      // post data
      this.$http.post(url, {codec: this.output}).then(function (res) {
        this.$router.push({
          name: 'ApplicationSettings',
          params: { id: this.$route.params.id }
        })
      }, response => {
        console.log(response)
        this.notifyError(response.status, response.body.message)
      })
    },
    loadApplication: function () {
      let url = Persistent.getters.baseUrl + '/api/v2/application/' + this.$route.params.id + ''
      this.$http.get(url).then(function (res) {
        if (res.data.application) {
          this.application = res.data.application
          EventBus.$emit('set-title', this.application.name + ' codec')
          if (res.data.application.codec) {
            this.codec = res.data.application.codec
            this.output = res.data.application.codec
          } else {
            // this is a 500 error, server shall have served an clean new config
            this.codec = DEFAULT_CODEC
            this.output = DEFAULT_CODEC
          }
        }
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },
    notifyError: function (code, message) {
      console.log(Events.apiError, code, message)
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    }
  }
}
</script>
<style>
.code .CodeMirror-wrap {
  height: 160px;
}
</style>
