function _AWS_POLICY_DOCUMENT (accountId, region) {
  return {
    'Version': '2012-10-17',
    'Statement': [{
      'Effect': 'Allow',
      'Action': ['iot:Connect'],
      'Resource': ['*']
    }, {
      'Effect': 'Allow',
      'Action': ['iot:Publish'],
      'Resource': ['*']
    }, {
      'Effect': 'Allow',
      'Action': ['iot:Subscribe'],
      'Resource': ['*']
    }]
  }
}

function _AWS_POLICY_TEMPLATE_BODY (accountId, region) {
  if (!region || !accountId) return false
  // 'ThingTypeName': 'bulk-type',

  return {
    Parameters: {
      'AWS::IoT::Certificate::CommonName': { 'Type': 'String' },
      'AWS::IoT::Certificate::Id': { 'Type': 'String' },
      'AWS::IoT::Certificate::OrganizationalUnit': { 'Type': 'String' }
    },
    Resources: {
      thing: {
        Type: 'AWS::IoT::Thing',
        Properties: {
          'ThingName': { 'Ref': 'AWS::IoT::Certificate::CommonName' },
          'ThingGroups': [{'Ref': 'AWS::IoT::Certificate::OrganizationalUnit'}]
        }
      },
      certificate: {
        Type: 'AWS::IoT::Certificate',
        Properties: {
          CertificateId: { Ref: 'AWS::IoT::Certificate::Id' },
          Status: 'ACTIVE'
        }
      },
      policy: {
        Type: 'AWS::IoT::Policy',
        Properties: {
          PolicyDocument: JSON.stringify(_AWS_POLICY_DOCUMENT())
        }
      }
    }
  }
}

function AWS_POLICY_TEMPLATE (accountId, region) {
  return {
    templateBody: JSON.stringify(_AWS_POLICY_TEMPLATE_BODY(accountId, region)),
    roleArn: `arn:aws:iam::${accountId}:role/JITPRole`
  }
}

export {
  AWS_POLICY_TEMPLATE
}
