
import { Line, mixins } from 'vue-chartjs'
const { reactiveProp } = mixins

export default {
  extends: Line,
  mixins: [reactiveProp],
  props: ['options', 'pagination'],
  mounted () {
    console.log('render chart!')
    this.renderChart(this.chartData, this.options)
  },
  watch: {
    pagination: function () {
      this.renderChart(this.chartData, this.options)
      console.log('line got page change')
    }
  }
}
