<template>
  <v-toolbar card prominent>
    <v-toolbar-title class="body-2 grey--text">Bluetooth provision</v-toolbar-title>

    <v-spacer></v-spacer>
    <v-progress-linear
      v-if="progress > 0 && progress < 100"
        v-model="progress"
        color="red">
    </v-progress-linear>

    <v-btn color="info black--text"
      raised
      @click.stop="ensureConfig()"
      v-if="loggedIn">
      <v-icon left>settings</v-icon>
      Ensure config
    </v-btn>
    <v-btn color="info black--text"
      raised
      @click.stop="openDumpFile()"
      v-if="loggedIn">
      <v-icon left>save_alt</v-icon>
      Dump file
    </v-btn>

    <v-btn
      @click="connect"
      v-if="!connected"
      color="blue black--text">
      <v-icon left>bluetooth_searching</v-icon>
      pair
    </v-btn>

    <v-btn
      @click="disconnect"
      v-if="connected"
      color="warning black--text">
      <v-icon left>bluetooth_searching</v-icon>
      disconnect
    </v-btn>

    <v-ble-pin-code
      v-on:disconnect="disconnect"
      v-on:onLogin="postLogin"
      :show="showPinCodeDialog">
    </v-ble-pin-code>

    <v-set-ble-pin-code
      v-on:registerPin="onRegisterPin"
      :show.sync="showRegisterDialog">
    </v-set-ble-pin-code>

    <v-ble-dump-file
      :progress="progress"
      :session="session"
      v-on:onClose="message=null; showBLEDumpFile = false; progress=-1"
      v-on:onChunk="onChunk"
      :show.sync="showBLEDumpFile">
    </v-ble-dump-file>
    <v-select-ble-tpl ref="selectBleTpl"></v-select-ble-tpl>
  </v-toolbar>

</template>

<script>
import axios from 'axios'
import SparkMD5 from 'spark-md5'
import Events from '../../store/event-api.js'
import EventBus from '../../event-bus.js'
import Persistent from '../../store/persistent'
import SetPincode from '@/components/ble/BLESetPinCode'
import BLEPincode from '@/components/ble/BLEPinCode'
import BLEDumpFile from '@/components/ble/BLEDumpFile'
import SelectBLETemplate from '@/components/ble/SelectBLETemplate'

const log = require('debug')('youspot-webapp:ble')

export default {
  name: 'BLE',
  components: {
    'v-set-ble-pin-code': SetPincode,
    'v-ble-pin-code': BLEPincode,
    'v-ble-dump-file': BLEDumpFile,
    'v-select-ble-tpl': SelectBLETemplate
  },
  data () {
    return {
      device: {},
      id: null,
      access_token: null,
      connected: false,
      bt_connection_error: false,
      loggedIn: false,
      writer: null,
      reader: null,
      read_buffer: new Uint8Array(),
      message: null,
      showRegisterDialog: false,
      showPinCodeDialog: false,
      showBLEDumpFile: false,
      session: null,
      progress: 0,
      action: ''
    }
  },
  methods: {
    notifyError: function (code, message) {
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    },
    async ensureConfig () {
      const t = await this.getV1AccessToken()
      if (!t.credentials) {
        return
      }

      const c = await this.getV1Configuration(t.credentials[0].access_token)
      const d = JSON.stringify(c)
      const db64 = btoa(d)
      var spark = new SparkMD5()
      spark.appendBinary(d)
      const md5 = spark.end()
      const path = '/flash/config.json'
      this.action = `sending ${path} ...`
      const message = `POST file\nmd5:${md5}\npath:${path}\naccess_token:${this.session}\n\n${db64}`
      let me = this
      this.send(message, function (m) {
        me.action = ''
        // TODO notifySuccess
      }).catch((e) => {
        // TODO notifyError
        console.log('error in sending file ...')
        console.log(e)
        this.notifyError(500, e)
      })
    },
    getV1Configuration (t) {
      this.action = 'resolve device configuration'
      return new Promise((resolve, reject) => {
        const url = Persistent.getters.baseUrl + '/api/v1/devices/me/configuration'
        axios.get(url, {
          headers: {
            Authorization: 'Bearer ' + t
          }}).then(function (res) {
          const result = res.data.configuration
          log('/api/v1/devices/me/configuration returned:', result)
          resolve(result)
        }, response => {
          reject(response)
        })
      })
    },
    getV1AccessToken () {
      this.action = 'resolve Authorization'
      return new Promise((resolve, reject) => {
        const url = Persistent.getters.baseUrl + `/api/v2/account/device/${this.device._id}/credentials`
        this.$http.get(url).then(function (res) {
          resolve(res.body)
        }, response => {
          reject(response)
        })
      })
    },
    onChunk (c) {
      let me = this
      const cb = function (m) {
        me.showBLEDumpFile = false
        // TODO notifySuccess
        me.action = ''
      }
      this.action = 'sending data'
      this.send(c, cb).catch((e) => {
        // TODO notifyError
        console.log('error in sending file ...')
        console.log(e)
        this.notifyError(500, e)
      })
    },
    openDumpFile () {
      this.progress = 0
      this.showBLEDumpFile = 1
    },
    disconnect () {
      if (this.peripheral && this.peripheral.gatt) {
        this.peripheral.gatt.disconnect()
        this.peripheral = null
      }
    },
    onDisconnected () {
      this.connected = false
      this.loggedIn = false
      this.session = null
      this.uname = null
      this.unique_id = null
      this.$emit('ble-off')
    },
    onConnected () {
      this.connected = true
      this.showPinCodeDialog = true
    },
    onRegisterPin (pin) {
      this.showRegisterDialog = false
      this.showPinCodeDialog = false
    },
    onLoginResponse (r) {
      const lines = r.split('\n')
      let payload = {}
      try {
        payload = JSON.parse(lines[lines.length - 1])
      } catch (e) {
        console.log('error onLoginResponse:', e)
        this.notifyError(500, e)
        this.disconnect()
        this.showPinCodeDialog = false
        return
      }
      this.showPinCodeDialog = false
      this.device.uname = payload.uname
      this.device.sysname = payload.sysname
      this.device.release = payload.release
      this.device.version = payload.version
      this.device.unique_id = payload.unique_id
      this.session = payload.session
      this.showPinCodeDialog = false
      this.loggedIn = true
      this.navigate()
    },
    postLogin (pin) {
      this.showRegisterDialog = false
      let me = this
      const message = 'POST login\n\n' + pin + '\n'
      this.send(message, me.onLoginResponse).catch((e) => {
        this.notifyError(500, e)
      })
    },
    async createOrCopyDevice () {
      if (this.$refs.selectBleTpl) {
        const tpl = await this.$refs.selectBleTpl.open()
        if (tpl) {
          const message = 'Use the bluetooth icon when you need to save your configuration on the device'
          EventBus.$emit(Events.apiSuccess, '', message)
          this.createDevice(tpl)
        } else {
          this.disconnect()
        }
      }
    },
    createDevice () {
      let url = Persistent.getters.baseUrl + '/api/v2/account/devices'
      log('creating a device:', this.device)
      let data = {
        name: 'new device',
        unique_id: this.device.unique_id,
        uname: this.device.uname,
        sysname: this.device.sysname,
        release: this.device.release,
        version: this.device.version
      }
      this.$http.put(url, data).then(function (res) {
        if (res.data.status === 200 && res.data.device._id) {
          // let some time for mongodb to flush ...
          this.device = res.data.device
          let me = this
          setTimeout(function () {
            me.$router.push({name: 'DeviceSettings', params: { id: me.device._id }})
          }, 800)
        } else {
          this.notifyError(res.status, res.data)
        }
      }, response => {
        this.notifyError(response.status, response.body.message)
      })
    },
    async navigate () {
      if (!this.device.unique_id) {
        this.notifyError(500, 'could get a machine identifier')
        console.log('no unique_id => disconnect')
        this.disconnect()
        return
      }
      let url = Persistent.getters.baseUrl + '/api/v2/account/devices?unique_id=' + this.device.unique_id
      this.$http.get(url).then(function (res) {
        if (res.body.devices !== null) {
          if (res.body.devices.length === 0) {
            this.createOrCopyDevice()
          } else {
            this.device = res.body.devices[0]
            this.id = res.body.devices[0]._id
            // this.access_token = this.getAccessToken(this.id)
            this.$router.push({name: 'DeviceSettings', params: { id: this.id }})
          }
        }
      }, response => {
        console.log('error could not navigate ...')
        this.notifyError(500, 'unable to navigate to device')
      })
    },
    send (message, cb) {
      let me = this
      return new Promise(function (resolve, reject) {
        me.callback = function (res) {
          // This is called even on 404 etc
          // so check the status,
          if (res.substring(0, 10).replace(/\D/g, '').startsWith('200')) {
            cb(res)
            resolve(res)
          } else {
            window.reload()
            // this.notifyError(500, 'send callback' + res.substring(0, 10))
            reject(Error('promise reject'))
          }
        }
        me.rwrite(message)
      })
    },
    rwrite (message, cursor = 0) {
      const HEAP = 20
      if (!EventBus.writer) {
        // @TODO notifyError
        console.log('attempt write attempt without connection')
        this.connected = false
        this.loggedIn = false
        this.session = null
        this.uname = null
        this.unique_id = null
        this.$emit('ble-off')
        return
      }
      if (message.length > cursor) {
        EventBus.writer.writeValue(new TextEncoder('utf-8').encode(message.substr(cursor, HEAP)))
          .then(_ => {
            if (this.progress !== -1) { // if not canceled
              this.rwrite(message, cursor + HEAP)
            }
          })
      } else {
        EventBus.writer.writeValue(new TextEncoder('utf-8').encode(''))
      }
      const pct = Math.round(cursor * 1000 / message.length) / 10
      if (pct !== this.progress) {
        this.progress = pct
      }
    },
    onMessage (message) {
      // early entry point of ble characteristics
      if (this.callback) {
        this.callback(message)
      } else {
        this.notifyError(500, 'no callback on BLE message')
        console.log('no callback for response')
      }
    },
    appendBuffer (buffer1, buffer2) {
      var tmp = new Uint8Array(buffer1.byteLength + buffer2.byteLength)
      tmp.set(new Uint8Array(buffer1), 0)
      tmp.set(new Uint8Array(buffer2), buffer1.byteLength)
      return tmp.buffer
    },
    onReaderChanged (event) {
      this.read_buffer = this.appendBuffer(this.read_buffer, event.target.value.buffer)
      if (new TextDecoder('utf-8').decode(event.target.value.buffer) === '') {
        var r = new TextDecoder('utf-8').decode(this.read_buffer)
        this.read_buffer = new Uint8Array(0).buffer
        this.onMessage(r)
      }
    },
    connect () {
      let me = this
      if (!navigator.bluetooth) {
        this.connected = false
        this.bt_connection_error = true
        this.loggedIn = false
        this.session = null
        this.uname = null
        this.unique_id = null
        setTimeout(() => {
          this.$emit('ble-off')
        }, 3000)
        return
      }
      navigator.bluetooth.requestDevice({
        filters: [ {namePrefix: 'youspot'} ],
        optionalServices: [0x0000, 0x0001]
      })
        .then(peripheral => {
          this.peripheral = peripheral
          this.peripheral.addEventListener('gattserverdisconnected', this.onDisconnected)
          // Persistent.commit('setDevice', device)
          return peripheral.gatt.connect()
        })
        .then(server => {
          server.getPrimaryServices().then(function (services) {
            for (let i = 0; i < services.length; i++) {
              services[i].getCharacteristics().then(function (characteristics) {
                const a = services[i].uuid
                // const b = characteristics[0].uuid
                if (a === '00000000-0000-1000-8000-00805f9b34fb') {
                  EventBus.writer = characteristics[0]
                  console.log('set writer ', services[i].uuid, characteristics[0].uuid)
                }
                if (services[i].uuid === a && a === '00000001-0000-1000-8000-00805f9b34fb') {
                  console.log('set reader ', services[i].uuid, characteristics[0].uuid)
                  EventBus.reader = characteristics[0]
                  characteristics[0].startNotifications()
                    .then(characteristic => {
                      EventBus.reader = characteristic
                      EventBus.reader.addEventListener('characteristicvaluechanged', me.onReaderChanged)
                      me.onConnected()
                      // return
                    })
                }
              })
            }
          //
          }).catch(function (e) {
            console.log(e)
            this.connected = false
            this.loggedIn = false
            this.session = null
            this.uname = null
            this.unique_id = null
            this.$emit('ble-off')
          })
        })
        .catch(error => {
          console.log(error)
          this.connected = false
          this.loggedIn = false
          this.session = null
          this.uname = null
          this.unique_id = null
          this.$emit('ble-off')
        })
    }
  }
}
</script>
