<template>
  <v-container fill-height justify-center  justify-start  fluid grid-list-sm>
    <v-layout  v-if="device" align-start wrap>

      <v-flex xs12 offset-md2 md8>
        <v-card class="upy-card">
          <v-device-card-title :device="device" :version="version"></v-device-card-title>
            <v-card-text>
              <v-layout row wrap pa-4 >
                <v-flex md2>
                <v-switch v-model="lte_on_boot" label="LTE on boot"></v-switch>
                </v-flex>
                <v-flex md10>
                  <v-spacer></v-spacer>
                </v-flex>
                <v-flex md12 pa-4>
                  <v-text-field label="APN"  v-model="apn"></v-text-field>
                </v-flex>
                <v-flex md3 pa-4>
                  <v-text-field label="band" type="number" v-model="band"></v-text-field>
                </v-flex>
                <v-flex md3 pa-4>
                  <v-text-field label="connection id" type="number" v-model="connection_id"></v-text-field>
                </v-flex>
                <v-flex md6 pa-4>
                  <v-text-field label="carrier"  v-model="carrier"></v-text-field>
                </v-flex>
            </v-layout>
          </v-card-text>
        <v-btn absolute fab bottom right color="primary" @click.native="saveLTE()">
          <v-icon>done</v-icon>
        </v-btn>
      </v-card>
    </v-flex>
  </v-layout>
</v-container>
</template>

<script>
import Events from '../../../store/event-api.js'
import EventBus from '../../../event-bus.js'
import Persistent from '../../../store/persistent'
import DeviceSettingsCardTitle from '../DeviceSettingsCardTitle.vue'

export default {
  name: 'DeviceLTESettings',
  props: ['configuration', 'device', 'version'],
  data: function () {
    return {
      carrier: null,
      band: null,
      apn: null,
      lte_on_boot: false,
      connection_id: null
    }
  },
  components: {
    'v-device-card-title': DeviceSettingsCardTitle
  },
  mounted () {
    EventBus.$emit('set-title', 'Device LTE settings')
    if (this.configuration) {
      const lte = this.configuration.lte || {}
      this.carrier = lte.carrier
      this.band = lte.band
      this.apn = lte.apn
      this.connection_id = lte.connection_id
      this.lte_on_boot = lte.lte_on_boot
    } else {
      EventBus.$emit(Events.loadingEnd)
      // redirect to device settings home
      this.$router.push({
        name: 'DeviceSettings',
        params: { id: this.$route.params.id }
      })
    }
  },
  methods: {
    saveLTE: function () {
      // finally post datas ///
      const lte = {
        carrier: this.carrier,
        band: this.band,
        apn: this.apn,
        lte_on_boot: this.lte_on_boot,
        connection_id: this.connection_id
      }
      let url = Persistent.getters.baseUrl + '/api/v2/account/device/' + this.$route.params.id + '/configuration'
      this.$http.post(url, { lte }).then(function (res) {
        if (res.status === 200) {
          // redirect to device settings home
          this.$router.push({
            name: 'DeviceSettings',
            params: { id: this.$route.params.id }
          })
        } else {
          this.notifyError(res.body.status, res.body.error)
        }
      }, response => {
        console.log('error updating LTE config:', response)
        this.notifyError(response.status, response.body.message)
      })
    },
    notifyError: function (code, message) {
      console.log(Events.apiError, code, message)
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    }
  }
}
</script>
