<template>

  <v-layout>
    <v-flex class="pa-1 ma-1 xs6 text-xs-center">
      <v-select :items="serialPorts2"
        v-if="!pending"
        v-model="serialPort"
        label="Serial ports"
        prepend-icon="refresh"
        @click:prepend="scanPorts">
      </v-select>
    <v-progress-circular  v-if="pending" :indeterminate="true"></v-progress-circular>
    <span v-if="pending">
      <p>scanning  ...</p>
    </span>
    </v-flex>

  </v-layout>

</template>

<script>
import config from '../../config'

export default {
  name: 'SerialPort',
  data: function () {
    return {
      pending: false
    }
  },
  computed: {
    serialPorts2 () {
      return this.$store.state.serialPorts
    },
    serialPort () {
      return this.$store.state.serialPort
    }
  },
  methods: {
    scanPorts () {
      this.pending = true
      let url = config.API_LOCATION + 'scan_ports'
      this.$http.get(url).then((response) => {
        this.$store.commit('updateSerialPorts', response.data)
        this.pending = false
        if (!this.$store.state.serialPort) {
          this.$store.commit('setSerialPort', response.data[0])
        }
      }).catch(e => {
        this.pending = false
      })
    }
  },
  mounted () {

  }
}
</script>

