<template>
      <v-dialog v-model="show" width="320">
        <v-card class="upy-card">
          <v-card-title>
            <h2>Bluetooth login</h2>
          </v-card-title>
          <v-card-text>
            <p>Please enter your pincode to access device</p>
            <v-layout wrap>
            <v-flex xs9>
                <v-text-field placeholder="1234" :rules="ruleReq" v-model="pinCode" label="Pincode" required v-if="show" autofocus></v-text-field>
            </v-flex>
          </v-layout>
          </v-card-text>
        <v-card-actions>
          <v-btn color="danger black--text" raised @click.stop="$emit('disconnect')">Disconnect</v-btn>
          <v-btn disabled:="pinCode" color="primary black--text" raised @click.stop="toggle()">Login</v-btn>
          </v-card-actions>
        </v-card>
   </v-dialog>
</template>

<script>
// import EventBus from '../event-bus.js'
export default {
  name: 'BLEPincode',
  props: ['show'],
  data () {
    return {
      pinCode: '',
      pinCode2: '',
      ruleReq: [
        (v) => !!v || 'Pincode is required'
      ]
    }
  },
  computed: {
    valid: function () {
      return this.pinCode.length > 0
    }
  },
  methods: {
    toggle () {
      this.$emit('onLogin', this.pinCode)
    }
  }
}
</script>
