<template>
  <v-flex xs12>
  <v-data-table
    hide-actions
    :headers="cols"
    :items="filters"
    class="elevation-1"
  >

    <template slot="items" slot-scope="props">
      <td>{{ props.item.key }}</td>
      <td>{{ props.item.operator }}</td>
      <td>{{ props.item.value }}</td>
      <td class="text-xs-right">
        <v-icon
          small
          class="mr-2"
          @click="editItem(props.item)"
        >
          edit
        </v-icon>
        <v-icon
          small
          @click="deleteItem(props.item)"
        >
          delete
        </v-icon>
      </td>
    </template>

    <template slot="footer">
          <v-btn v-if="filters.length === 0" @click="initialize">Reset</v-btn>
          <v-dialog v-model="dialog" max-width="580px">
            <v-btn slot="activator" class="mb-2">New message filter</v-btn>
            <v-card class="upy-card">
              <v-card-title>
                <span class="headline">{{ formTitle }}</span>
              </v-card-title>
              <v-card-text>
                <v-container grid-list-md>
                  <v-layout wrap justify-end align-end>
                    <v-flex xs12 sm6 md4>
                      <v-select
                        disabled
                        :items="keys"
                        label="Key"
                        v-model="editedItem.key"
                      ></v-select>
                      <v-text-field

                        :rules="keyRules"
                        v-if="editedItem.key !== 'topic'"
                        v-model="editedItem.key"
                        :label="editedItem.key.split('.')[0]">
                      </v-text-field>
                    </v-flex>
                    <v-flex xs12 sm6 md4>
                      <v-select
                        disabled
                        :items="operators"
                        label="Operators"
                        v-model="editedItem.operator"
                      >

                    </v-select>
                    </v-flex>
                    <v-flex   text-xs-right align-end xs12 sm6 md4>
                      <v-spacer></v-spacer>
                      <v-text-field v-model="editedItem.value" label="Value"></v-text-field>
                    </v-flex>
                  </v-layout>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" flat @click="close">Cancel</v-btn>
                <v-btn color="blue darken-1" flat @click="save">Save</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
    </template>
  </v-data-table>
</v-flex>
</template>

<script>
// import EventBus from '../../event-bus.js'
// import Events from '../../store/event-api.js'

// const OPERATORS = [
//  {key: 'LT', value: '<'},
//  {key: 'MATCH', value: 'match'},
//  {key: 'EQ', value: '=='},
//  {key: 'GT', value: '>'}
// ]

const DEFAULT_FILTERS = [
  {
    key: 'topic',
    operator: 'MATCH',
    value: '*'
  }
]

export default {
  props: ['filters'],
  name: 'MessageFilter',
  watch: {
    dialog (val) {
      if (this.editedIndex === -1) {
        this.editedItem = Object.assign({}, this.defaultItem)
      }
      console.log('dialog open with', this.editedItem)
    }
  },
  data () {
    return {
      keyRules: [
        (v) => !!v || 'Key is required',
        // ^[data|attributes|topic].*[^\.]$
        (v) => /^[data|attributes|topic].*[^.]$/.test(v) || 'Key must be valid'
      ],
      filterMode: 'topic',
      keys: [
        { value: 'topic', text: 'topic' },
        { value: 'data.', text: 'data' },
        { value: 'attributes.', text: 'attributes' }
      ],
      operators: [
        { value: 'MATCH', text: 'match' },
        { value: 'GT', text: 'greater than' },
        { value: 'LT', text: 'lower than' },
        { value: 'EQ', text: 'equal' }
      ],
      dialog: false,
      cols: [
        { text: 'key', align: 'left', value: 'value', sortable: false },
        { text: 'operator', align: 'left', value: 'value', sortable: false },
        { text: 'value', align: 'left', value: 'edit', sortable: false },
        { text: 'edit', align: 'left', value: 'edit', sortable: false }
      ],
      editedIndex: -1,
      editedItem: {
        key: '',
        value: ''
      },
      defaultItem: {
        key: 'topic',
        operator: 'MATCH',
        value: '/'
      }
    }
  },
  computed: {
    formTitle: function () {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    }
  },
  methods: {
    initialize () {
      this.filters = DEFAULT_FILTERS.slice(0)
      console.log('initialize!')
    },
    editItem (item) {
      console.log('editItem')
      this.editedIndex = this.filters.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },
    deleteItem (item) {
      const index = this.filters.indexOf(item)
      confirm('Are you sure you want to delete this item?') && this.filters.splice(index, 1)
      this.$emit('onChange', this.filters)
    },
    close () {
      this.dialog = false
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
        this.$emit('onChange', this.filters)
      }, 300)
    },
    save () {
      if (this.editedIndex > -1) {
        Object.assign(this.filters[this.editedIndex], this.editedItem)
      } else {
        this.filters.push(this.editedItem)
      }
      this.close()
    }
  }
}
</script>
