<template>
  <v-layout style=" margin:0;">

  <v-flex  fill-height>

  <v-img :height="imgheight" width="100%" position="top"
  src="/static/map.png"></v-img>

  <v-card v-if="message" class="message-card">
    <v-card-text style="min-height: 380px;">
    <p class="display-2 accent--text">
      {{message}}</p>
  </v-card-text>
  </v-card>

</v-flex>

<v-dialog-login :dialog="loginModal"></v-dialog-login>

<span class="download-span">
  <v-btn large
    href="https://youspot.me"
    target="_blank"
    rel="noopener noreferrer"
    class="warning" >
    https://youspot.me
  </v-btn>
</span>
</v-layout>
</template>

<script>
import EventBus from '../event-bus.js'
import Events from '../store/event-api.js'
import Persistent from '../store/persistent'
import JwtDecode from 'jwt-decode'
import Vue from 'vue'
import DialogLogin from '@/components/global/DialogLogin'

export default {
  name: 'Welcome',
  data () {
    return {
      imgheight: window.height - 120,
      message: null,
      charIdx: 0,
      welcome: 'Welcome agent, this is ℿblueweb from µℼ=f(x), please login',
      openLogin: false,
      login: null,
      password: null,
      baseUrl: Persistent.getters.baseUrl,
      ruleReq: [
        (v) => !!v || 'Field is required'
      ],
      emailRules: [
        (v) => !!v || 'E-mail is required',
        /* eslint-disable no-useless-escape */
        (v) => /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'E-mail must be valid'
      ]
    }
  },
  components: {
    'v-dialog-login': DialogLogin
  },
  mounted: function () {
    EventBus.$emit('set-title', 'youspot.me')
    window.scrollTo(0, document.body.scrollHeight)
    var me = this
    setTimeout(function () {
      me.scrollToTop(1500)
    }, 1000)
    const token = $cookies.get('token')
    if (token !== null && token !== 'null') {
      Persistent.commit('addAuthToken', token)
      try {
        JwtDecode(token)
      } catch (e) {
        console.log(e)
        this.cleanLogin()
        return
      }
      this.setUpHttpHeader(token)
      // verify ...
      let url = Persistent.getters.baseUrl + '/api/auth/userinfo'
      this.$http.get(url).then((res) => {
        Persistent.commit('setUser', res.data.user)
        window.scrollTo(0, document.body.scrollHeight)
        this.welcomeHim(res.body.user)
        this.openLogin = false
      }, response => {
        this.notifyError(response.status, response.body.message)
        this.cleanLogin()
      })
    } else {
      this.cleanLogin()
    }
  },
  computed: {
    isLoggedIn () {
      return Persistent.getters.user !== null
    },
    loginModal () {
      return !this.message && this.openLogin
    },
    isDark () {
      const d = false
      return Persistent.getters.user ? Persistent.getters.user.darkTheme : d
    }
  },
  methods: {
    scrollToTop: function (scrollDuration) {
      var cosParameter = window.scrollY / 2
      var scrollCount = 0
      var oldTimestamp = performance.now()
      function step (newTimestamp) {
        scrollCount += Math.PI / (scrollDuration / (newTimestamp - oldTimestamp))
        if (scrollCount >= Math.PI) window.scrollTo(0, 0)
        if (window.scrollY === 0) return
        window.scrollTo(0, Math.round(cosParameter + cosParameter * Math.cos(scrollCount)))
        oldTimestamp = newTimestamp
        window.requestAnimationFrame(step)
      }
      window.requestAnimationFrame(step)
    },
    welcomeHim: function (user) {
      Persistent.commit('setUser', user)
      EventBus.$emit(Events.loadingEnd)
      this.openLogin = false
      this.welcome = `Nice to meet you ${user.fullName}, youspot.me is ready to use.`
      this.writeMessage()
    },
    cleanLogin: function () {
      this.openLogin = true
      $cookies.set('token', null)
      Persistent.commit('setUser', null)
      Persistent.commit('deleteAuthToken')
      this.openLogin = true
      this.welcome = 'Hello Navigator! Nice to have you around, please login.'
      this.writeMessage()
    },
    notifyError: function (code, message) {
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    },
    setUpHttpHeader: function (token) {
      Vue.http.interceptors.push((request, next) => {
        request.headers.set('Authorization', 'Bearer ' + token)
        request.headers.set('Accept', 'application/json')
        next()
      })
    },
    writeMessage: function () {
      const self = this
      this.charIdx++
      if (this.charIdx > this.welcome.length) {
        this.onMessageEnd()
        return
      }
      this.message = this.welcome.substring(0, this.charIdx)
      setTimeout(function () { self.writeMessage() }, 13 + Math.floor(Math.random() * 100) + 1)
    },
    onMessageEnd: function () {
      if (this.isLoggedIn) {
        EventBus.$emit(Events.toggleDrawer)
      } else {
        this.openLogin = true
      }
      let me = this
      setTimeout(function () {
        me.message = null
      }, 1200)
    }
  }
}
</script>
<style>
.download-span {
  position: fixed;
  bottom: 16px;
  left: 42px;
  z-index: 999;
}

.message-card {
  background-color: transparent;
  position: fixed!important;
  bottom: 16px!important;
  margin-left: 36px!important;
  right: 12px!important;
  width: 330px!important;
  z-index: 1000!important;
  border: 0!important;
  opacity: 0.9;
  border-radius: 4px;
}
.mirror {
    opacity: 0.8;
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
}

.v-dialog {
    position: absolute;
    top: 24px;
}
</style>
