<template>
  <v-container fill-height justify-center  justify-start  fluid grid-list-sm>
    <v-layout  v-if="device" align-start wrap>
      <v-flex xs12>
        <v-card class="upy-card">
          <v-device-card-title :device="device" :version="version"></v-device-card-title>
          <v-card-text>
            <v-layout>
            <v-flex md2>
            <v-switch  v-model="wifi_on_boot" label="Wifi on boot"></v-switch>
            </v-flex>
            <v-flex md2>
            <v-switch  v-model="ntp_sync" label="Set time with Internet"></v-switch>
            </v-flex>
            <v-flex md2>
            <v-switch  v-model="external_antenna" label="External antenna"></v-switch>
            </v-flex>
          </v-layout>
            <v-data-table
              :headers="headers"
              :items="wifis"
              hide-actions
              class="elevation-1"
            >
            <template slot="items" slot-scope="props">
              <v-wifi-line v-on:delete="deleteNetwork" v-on:change="updateNetworks" :index="props.index" :data="props.item"></v-wifi-line>
            </template>
          </v-data-table>
          </v-card-text>
          <v-card-actions>
            <v-btn color="primary" raised @click.stop="addWifi()">Add Wifi Credential</v-btn>
          </v-card-actions>
      <v-btn absolute fab bottom right color="primary" @click.native="saveWifis()">
          <v-icon>done</v-icon>
      </v-btn>
      </v-card>

    </v-flex>

  </v-layout>
</v-container>
</template>

<script>
import Events from '../../../store/event-api.js'
import EventBus from '../../../event-bus.js'
import PasswordToggle from '../../global/PasswordToggle'
import Persistent from '../../../store/persistent'
import DeviceWifiSettingsLine from './DeviceWifiSettingsLine'
import DeviceSettingsCardTitle from '../DeviceSettingsCardTitle.vue'

export default {
  name: 'WifiSettings',
  props: ['configuration', 'device', 'version'],
  data: function () {
    return {
      headers: [
        { text: 'authentication', value: 'authentication', sortable: false },
        { text: 'bssid', value: 'bssid', sortable: false },
        { text: '', value: 'password', sortable: false },
        { text: '', value: 'cert_auths', sortable: false },
        { text: '', value: 'client_cert', sortable: false },
        { text: '', value: 'client_key', sortable: false },
        { text: '', value: '', sortable: false },
        { text: '', value: '', sortable: false },
        { text: '', value: '', sortable: false }
      ],
      external_antenna: false,
      wifi_on_boot: false,
      ntp_sync: false,
      wifis: []
    }
  },
  components: {
    'v-password-field': PasswordToggle,
    'v-wifi-line': DeviceWifiSettingsLine,
    'v-device-card-title': DeviceSettingsCardTitle
  },
  mounted () {
    EventBus.$emit('set-title', 'Device wifi settings')
    if (this.configuration && this.configuration.wifi) {
      this.wifis = this.configuration.wifi.networks || []
      this.wifi_on_boot = this.configuration.wifi.wifi_on_boot
      this.ntp_sync = this.configuration.wifi.ntp_sync
    } else {
      // EventBus.$emit(Events.apiError, 500, 'Oupsss, app interface crashed ...')
      EventBus.$emit(Events.loadingEnd)
      // redirect to device settings home
      // this.$router.push({
      //   name: 'DeviceSettings',
      //   params: { id: this.$route.params.id }
      // })
    }
  },
  methods: {
    updateNetworks: function (index, wifi) {
      this.wifis[index] = wifi
      console.log('received update', wifi)
    },
    deleteNetwork: function (index, wifi) {
      console.log('delete', index)
      this.wifis.splice(index, 1)
    },
    addWifi: function () {
      this.wifis.push({auth_type: 'WPA2', bssid: '', password: ''})
    },
    deleteWifi: function (bssid) {
      this.wifis = this.wifis.filter(item => item.bssid !== bssid)
    },
    saveWifis: function () {
      // remove empty bssid
      const wifis = this.wifis.filter(item => item.bssid !== '')
      // clean transient keys
      wifis.forEach(function (v) { delete v.show })
      // finally post datas ///
      const wifi = {
        networks: wifis,
        wifi_on_boot: this.wifi_on_boot,
        external_antenna: this.external_antenna,
        ntp_sync: this.ntp_sync
      }
      let url = Persistent.getters.baseUrl + '/api/v2/account/device/' + this.$route.params.id + '/configuration'
      this.$http.post(url, { wifi }).then(function (res) {
        if (res.status === 200) {
          // redirect to device settings home
          this.$router.push({
            name: 'DeviceSettings',
            params: { id: this.$route.params.id }
          })
        } else {
          this.notifyError(res.body.status, res.body.error)
        }
      }, response => {
        console.log('error updating wifis:', response)
        this.notifyError(response.status, response.body.message)
      })
    },
    notifyError: function (code, message) {
      console.log(Events.apiError, code, message)
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    }
  }
}
</script>
