<template>
  <div class="text-xs-right">

      <v-btn class="primary"  :disabled='!playing && !stream' @click="stop">
      <v-icon left  color="success">stop</v-icon>
      Stop
    </v-btn>
      <v-btn class="primary"  :disabled='stream && playing' @click="start">
      <v-icon left   color="success">play_arrow</v-icon>
      Play
    </v-btn>
  </div>
</template>

<script>
import mqtt from 'mqtt'
import Events from '../../store/event-api.js'
import Persistent from '../../store/persistent'
import EventBus from '../../event-bus.js'

export default {
  name: 'DeviceStream',
  props: ['device', 'stream'],
  data () {
    return {
      playing: false,
      visible: false,
      client: null,
      user: Persistent.getters.user,
      message: {},
      topic: ''
    }
  },
  watch: {
    stream: function (v) {
      if (v === true && !this.playing) {
        this.start()
      }
      if (v === false && this.playing) {
        this.stop()
      }
    }
  },
  mounted () {
    if (this.client) {
      this.client.end()
    }
    // this.getMQTTClient()
  },

  beforeDestroy () {
    if (this.client) {
      this.client.end()
    }
  },
  methods: {
    start () {
      this.$emit('onState', true)
      this.playing = true
      this.getMQTTClient()
    },
    stop () {
      this.$emit('onState', false)
      this.playing = false
      if (this.client) {
        this.client.end()
      }
      this.client = null
    },
    getMQTTClient () {
      if (!this.device.access_tokens || this.device.access_tokens.length < 1) {
        this.notifyError(500, 'device has no access token')
        return null
      }

      const username = this.device.access_tokens[0].access_token // + 'error'
      var options = {
        clientId: 'upy-blue' + this.device._id,
        clean: true,
        username,
        protocolId: 'MQTT',
        protocolVersion: 3,
        reconnectPeriod: 5000,
        path: '/mqtt'
      }
      var me = this
      const server = window.location.hostname
      var client = mqtt.connect(`wss://${server}:8001/`, options)
      this.client = client
      // onConnect
      client.on('connect', function () {
        const topic = `/v1/${me.device._id}/#`
        client.subscribe(topic, function (err) {
          if (me.stream && err) {
            // me.notifyError(500, err) // maybe err.toString()
            return
          }
          me.playing = true
          console.log('subscribed to', topic)
          me.notifySuccess(200, `subscribed to ${topic}`)
        })
      })
      // client.publish('presence', 'Hello mqtt')
      // onMessage
      client.on('message', function (topic, message) {
        // message is Buffer
        me.topic = topic
        me.message = message
        me.$emit('onData', topic, message)
      })
      // onError
      client.on('error', function (err) {
        // me.playing = false
        me.notifySuccess(500, err) // maybe err.toString()
        // client.end()
      })
      // onClose
      client.on('close', function () {
        me.notifySuccess(200, 'data stream closed')
        me.playing = false
        client.end()
      })
      // onMessage
      client.on('offline', function () {
        console.log('wss went offline')
        me.playing = false
        client.end()
      })
      return client
    },
    notifyError: function (code, message) {
      console.log(Events.apiError, code, message)
      // EventBus.$emit(Events.apiError, code, message)
      // EventBus.$emit(Events.loadingEnd)
      this.$emit('onState', false)
    },
    notifySuccess: function (code, message) {
      EventBus.$emit(Events.apiSuccess, code, message)
    }
  }
}

</script>
