<template>
  <v-app id="app" :dark="isDarkTheme" :light="!isDarkTheme">
    <v-drawer></v-drawer>
    <v-toolbar
      :isLoggedIn="isLoggedIn"
      :title="title"
      v-on:ble-on="bleOn = !bleOn"
      :bleOn="bleOn">
    </v-toolbar>
      <v-ble  v-on:ble-off="bleOn = false" v-if="bleOn"></v-ble>
    <v-progress-linear v-if="loading" v-bind:indeterminate="true"></v-progress-linear>
    <v-content >
      <v-container fluid fill-height ma-0 pa-0>
        <v-layout
        ma-0
        pa-0
        >
        <router-view />
            </v-layout>
        </v-container>

      <v-snackbar
        v-model="globalSnackbar.active"
        :bottom="globalSnackbar.bottom"
        :top="globalSnackbar.top"
        :left="globalSnackbar.left"
        :right="globalSnackbar.right"
        :multi-line="globalSnackbar.multiple"
        :vertical="globalSnackbar.vertical"
        :timeout="globalSnackbar.timeout"
        :color="globalSnackbar.mainColor"
      >
        {{ globalSnackbar.message }}
        <v-btn
          :color="globalSnackbar.btnColor"
          flat
          @click="globalSnackbar.active = false"
        >
        Close
        </v-btn>
      </v-snackbar>
    </v-content>
  </v-app>

</template>

<script>
import Vue from 'vue'
import Toolbar from '@/components/Toolbar'
import Drawer from '@/components/Drawer'
import EventBus from './event-bus.js'
import Persistent from './store/persistent'
import Events from './store/event-api.js'
import BLE from '@/components/ble/BLE'
import JwtDecode from 'jwt-decode'
import AppTheme from './AppTheme'

const DEV_HOST = 'http://localhost:8080'

export default {
  name: 'app',
  data () {
    return {
      globalSnackbar: {
        active: false,
        bottom: true,
        top: false,
        left: false,
        right: false,
        vertical: false,
        multiple: true,
        timeout: 6000,
        message: '',
        btnColor: 'pink',
        mainColor: 'default'
      },
      snackbar: false,
      message: null,
      bleOn: false,
      loading: false,
      successMessage: '',
      title: 'youspot.me'
    }
  },
  components: {
    'v-toolbar': Toolbar,
    'v-drawer': Drawer,
    'v-ble': BLE
  },
  methods: {
    setupTheme: function () {
      if (this.isDarkTheme) {
        this.$vuetify.theme = AppTheme.dark
      } else {
        this.$vuetify.theme = AppTheme.light
      }
    },
    setUpHttpHeader: function (token) {
      Vue.http.interceptors.push((request, next) => {
        request.headers.set('Authorization', 'Bearer ' + token)
        request.headers.set('Accept', 'application/json')
        next()
      })
    },
    cleanLogin: function () {
      this.openLogin = true
      $cookies.set('token', null)
      Persistent.commit('setUser', null)
      Persistent.commit('deleteAuthToken')
      this.openLogin = true
    }
  },
  computed: {
    isLoggedIn () {
      return Persistent.getters.user !== null
    },
    loginModal () {
      return this.openLogin
    },
    isDarkTheme () {
      return Persistent.getters.isDarkTheme
    },
    user () {
      return Persistent.getters.user
    }
  },
  watch: {
    isDarkTheme: function (val) {
      this.setupTheme()
    }
  },
  created: function () {
    this.setupTheme()
    if (process.env.NODE_ENV === 'development') {
      Persistent.commit('setBaseUrl', DEV_HOST)
    } else {
      Persistent.commit('setBaseUrl', '')
    }
    const token = $cookies.get('token')
    if (token !== null && token !== 'null') {
      Persistent.commit('addAuthToken', token)
      try {
        JwtDecode(token)
      } catch (e) {
        console.log(e)
        this.cleanLogin()
        return
      }
      this.setUpHttpHeader(token)
      // verify ...
      let url = Persistent.getters.baseUrl + '/api/auth/userinfo'
      this.$http.get(url).then((res) => {
        Persistent.commit('setUser', res.data.user)
        window.scrollTo(0, document.body.scrollHeight)
        // this.welcomeHim(res.body.user)
        this.openLogin = false
      }, response => {
        this.notifyError(response.status, response.body.message)
        this.cleanLogin()
      })
    } else {
      this.cleanLogin()
    }
    var me = this
    EventBus.$on(Events.apiError, function (status, message) {
      window.scrollTo(0, 0)
      if (status === 0) {
        status = ''
        message = 'Server is unreachable, is your network OK ?'
      }
      if (status === 401) {
        message = 'Authentication error'
        setTimeout(function () {
          Persistent.commit('deleteAuthToken')
          window.location = '/'
        }, 1000)
      }
      let alertMessage = status + ' ' + message
      me.globalSnackbar.message = alertMessage
      me.globalSnackbar.active = true
      me.globalSnackbar.mainColor = 'warning'
      me.globalSnackbar.btnColor = 'default'
    })
    EventBus.$on(Events.apiSuccess, function (code, message) {
      me.globalSnackbar.message = message
      me.globalSnackbar.active = true
      me.globalSnackbar.mainColor = 'info'
      me.globalSnackbar.btnColor = 'pink'
    })
    EventBus.$on(Events.loadingStart, function () {
      me.loading = true
    })
    EventBus.$on('set-title', function (t) {
      me.title = t
    })
    EventBus.$on(Events.loadingEnd, function () {
      me.loading = false
    })
  }
}
</script>
