<template>
  <v-layout>
    <v-flex mt-2 xs12 offset-md1 md10>
      <v-card class="upy-card">
        <v-card-text pb-4 class="pb-5">
          <v-layout grid wrap>
            <v-flex xs6>
              <v-switch
              class='mb-0'
              v-model="cloudOn"
              label="Enable Cloud Integration">
            </v-switch>
            <v-select class='mr-4 mt-0'
              v-if="cloudOn"
              :items="expires"
              label="Signed certificates expires after"
              v-model="deviceExpire"
            ></v-select>
            </v-flex>
            <v-flex v-if="cloudOn" xs6>
              <v-radio-group v-model="kind" :mandatory="false">
                <v-radio label="Amazon IoT Hub" value="AWS_CA"></v-radio>
                <v-radio disabled label="Azure IoT Hub" value="AZURE_CA"></v-radio>
              </v-radio-group>
            </v-flex>
            <v-flex v-if="cloudOn" xs12>
              <v-form v-model="valid">
              <v-text-field
                @change="saveWizard(false)"
                :rules='awsEndpointRules'
                :hint="endpointhint"
                label="endpoint"
                v-model="endpoint">
              </v-text-field>
            </v-form>

            </v-flex>
            <v-flex xs12 v-if="cloudOn && valid" >
              <p>Intermediate CAs</p>
              <v-data-table
                hide-actions
                :headers="tabHeaders"
                :items="certificates"
                class="elevation-1">
                <template slot="items" slot-scope="props">
                  <td>
                    <v-radio-group v-model="activeCA">
                    <v-radio
                      :value="props.item._id"
                    ></v-radio>
                  </v-radio-group>
                  </td>
                  <td class="text-truncate">
                    {{ props.item.decoded && props.item.decoded.serial }}
                    <br>
                    {{ props.item.data.registrationCode && 'registration code ' + props.item.data.registrationCode.substr(0, 16)+'...'  }}
                    {{ props.item.data.AWSCertificateId && 'certificate-id ' + props.item.data.AWSCertificateId  }}

                    <br>
                    <v-btn
                      v-if="!props.item.data || !props.item.data.registrationCode"
                      @click="startAWSRegistration(props.item._id)"
                      small
                      class="warning">
                      <v-icon>verified_user</v-icon>
                      sign registration code
                    </v-btn>

                  </td>
                  <td>{{ props.item.decoded && new Date(props.item.decoded.notBefore).toLocaleDateString()  }}</td>
                  <td>{{ props.item.decoded && new Date(props.item.decoded.notAfter).toLocaleDateString()  }}</td>
                  <td class="text-xs-center">
                    <v-btn
                    small
                      @click="deleteCA(props.item._id)">
                      <v-icon>delete</v-icon>
                      delete
                    </v-btn>

                  </td>
                </template>
              </v-data-table>

            </v-flex>

            <v-flex class="text-xs-center pt-2 xs12" v-if="cloudOn && valid">
              <v-btn
              class="info"
              small
              right
                @click="createCA()">
                <v-icon>add</v-icon>
                create a CA
              </v-btn>
            </v-flex>

             <v-flex v-if="hasCert">
              <p> Activate on AWS IoT Hub</p>
              x {{ this.activeCA }} x
               <v-expansion-panel >
                 <v-expansion-panel-content>
                   <div slot="header">
                     <v-icon left color="warning">cloud_download</v-icon>
                     Download the following files
                   </div>
                  <v-card class="md-6">
                    <v-card-text class="md-6">
                      <p v-if="!activeCA" class="error text-xs-center">activate a certificate</p>

                   <v-btn v-if="hasCert"
                   @click="download(awsCA.data.cert, project.slug + '-ca.pem')"
                     small
                     :enabled="awsCA.data.cert !== null"
                     class="info">
                     <v-icon left >cloud_download</v-icon>
                     {{ project.slug }}-ca.pem
                   </v-btn>

                   <v-btn v-if="hasCert"
                         small
                          :enabled="awsVerificationCA !== null"
                   @click="download(awsVerificationCA, project.slug + '-verification.pem')"

                     class="info">
                     <v-icon left >cloud_download</v-icon>
                     {{ project.slug }}-verification.pem
                   </v-btn>

                   <v-btn    small
                     @click="downloadAWSPolicyTemplate()"
                     v-if="hasCert"
                     class="info">
                     <v-icon left >cloud_download</v-icon>
                     {{ project.slug }}-policy-template.json
                   </v-btn>

                 </v-card-text>
                 </v-card>
               </v-expansion-panel-content>

               <v-expansion-panel-content>
                 <div slot="header">
                   <v-icon left color="warning" >code</v-icon>
                   Run the following commands</div>
                 <v-card>
                   <v-card-text>
                     <p>please check you have created a JITPRole:
                       <code>aws iam get-role --role-name JITPROLE</code>
                     </p>
                     <p>
                       <code class="mycode">aws iot create-thing-group --thing-group-name {{ project.slug }} </code>
                     </p>
                     <p>
                     <code class="mycode">aws iot register-ca-certificate --ca-certificate file://{{project.slug}}-CA.pem \
                       --verification-cert file://{{project.slug}}-verification.pem</code>
                   </p>
                   <p>
                     <v-text-field
                     value="certificateId"
                     @change="saveWizard(false)"
                     prepend-icon="edit"
                       label="please enter your certificate-id"
                       v-model="certificateId">
                     </v-text-field>
                   </p>
                   <p>
                     <code class="mycode">aws iot update-ca-certificate  --new-status ACTIVE \
    --certificate-id {{ certificateId || 'xxxxxxxxxxx' }} </code>
                   </p>
                     <p><code class="mycode">aws iot update-ca-certificate \
    --certificate-id {{ certificateId || 'xxxxxxxxxxx' }}  \
    --new-auto-registration-status ENABLE \
    --registration-config file://{{project.slug}}-policy-template.json</code></p>
                   </v-card-text>
                   </v-card>
                 </v-expansion-panel-content>
             </v-expansion-panel>
            </v-flex>
          </v-layout>
        </v-card-text>
        <v-card-actions>
          <v-btn
            flat
            href="https://youspot.gitbook.io/documentation/documentation/aws-iot-integration"
            class="info">
            <v-icon left>help</v-icon>
            help
          </v-btn>
        </v-card-actions>
        <v-btn  absolute right bottom fab @click="saveWizard(false)" color="info">
            <v-icon>done</v-icon>
        </v-btn>
      </v-card>
      <v-dialog-name ref="registrationCode"></v-dialog-name>
      <v-confirm ref="confirm"></v-confirm>

      </v-flex>

  </v-layout>
</template>

<script>
import EventBus from '../../event-bus.js'
import Events from '../../store/event-api.js'
import Persistent from '../../store/persistent'
import DialogName from '@/components/global/DialogName'
import DialogConfirm from '@/components/global/DialogConfirm'
import {AWS_POLICY_TEMPLATE} from './AWSProvision'

export default {
  name: 'ProjectIntegrations',
  components: {
    'v-dialog-name': DialogName,
    'v-confirm': DialogConfirm
  },
  data () {
    return {
      // https://golang.org/pkg/time/#ParseDuration
      expires: [
        { value: '26280h', text: '3 years' },
        { value: '8760h', text: '1 year' },
        { value: '2190h', text: '3 months' },
        { value: '72h', text: '3 days' }
      ],
      deviceExpire: '3y',
      activeCA: null,
      certificateId: null,
      valid: false,
      project: {},
      user: Persistent.getters.user,
      cloudOn: false,
      kind: 'AWS_CA',
      endpoint: '',
      cloudComplete: false,
      certificates: [],
      awsEndpointRules: [
        v => !!v || 'endpoint is required, use aws iot describe-endpoint to get it.',
        v => /[\w]\.iot\.(ap-south-1|eu-west-3|eu-west-2|eu-west-1|ap-northeast-3|ap-northeast-2|ap-northeast-1|sa-east-1|ca-central-1|ap-southeast-1|ap-southeast-2|eu-central-1|us-east-1|us-east-2|us-west-1|us-west-2)\.amazonaws\.com/.test(v) || 'This is not a valid AWS endpoint' ],
      tabHeaders: [
        { text: 'current', value: 'current' },
        { text: 'fingerprint', value: 'fingerprint' },
        { text: 'not before', value: 'notBefore' },
        { text: 'not after', value: 'notAfter' },
        { text: 'action', value: 'action', sortable: false }
      ]
    }
  },
  computed: {
    tabedCerts () {
      return []
    },
    hasCert () {
      for (let i = 0; i < this.certificates.length; i++) {
        const c = this.certificates[i]
        if (c.kind === 'AWS_CA' && c.data.cert) {
          return true
        }
      }
      return false
    },
    awsCA () {
      for (let i = 0; i < this.certificates.length; i++) {
        const c = this.certificates[i]
        console.log(c)
        if (c.kind === 'AWS_CA' &&
         c.data.cert) {
          return c
        }
      }
      this.notifyError(500, 'No certificate of kind AWS_CA found in active CA')
      return null
    },
    awsVerificationCA () {
      if (!this.activeCA) {
        this.notifyError(500, 'please active a CA using the radio button')
        return null
      }
      for (let i = 0; i < this.certificates.length; i++) {
        const c = this.certificates[i]
        if (c._id === this.activeCA) {
          if (c.kind === 'AWS_CA' &&
           c.data.verificationCert) {
            return c.data.verificationCert
          }
        }
      }
      this.notifyError(500, 'no verification certificate in active CA')
      return null
    },
    mqttHook () {
      let o = {}
      o.enabled = this.cloudOn
      o.activeCA = this.activeCA
      o.deviceExpire = this.deviceExpire
      o[this.kind] = {
        endpoint: this.endpoint,
        certificateId: this.certificateId
      }
      return o
    },
    endpointhint () {
      if (this.endpoint === '' && this.kind === 'AWS_CA') {
        return 'exemple: abcdefghijklmno.iot.us-east-1.amazonaws.com'
      } else {
        return null
      }
    }
  },
  mounted () {
    this.loadProject(this.$route.params.id)
    EventBus.$emit('set-title', 'project integrations')
  },
  methods: {
    createCA: async function (id) {
      let url = Persistent.getters.baseUrl +
        '/api/v2/project/' +
        this.$route.params.id +
        '/certificate/'
      this.$http.put(url).then(function (res) {
        if (res.body && res.body.status === 200) {
          this.loadProject()
          // this.parseResponse(res.body)
        } else {
          this.notifyError(res.body.status, res.body)
        }
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },
    deleteCA: async function (id) {
      if (await this.$refs.confirm.open('Delete CA', 'Are you sure?', {
        color: 'warning'
      })) {
        let url = Persistent.getters.baseUrl +
          '/api/v2/project/' +
          this.$route.params.id +
          '/certificate/' +
          id
        this.$http.delete(url).then(function (res) {
          this.loadProject(this.$route.params.id)
        }, response => {
          this.notifyError(response.status, response.statusText)
        })
      }
    },
    getAWSRegion: function () {
      return this.endpoint.split('.')[2]
    },
    startAWSRegistration: async function (certId) {
      const registrationCode = await this.$refs.registrationCode
        .open('Registration code',
          'Please enter registration code you got from: aws iot get-registration-code', {
            color: 'info'
          })
      if (registrationCode.length !== 64) {
        this.notifyError(403, 'registration code has length of 64 characters')
        return
      }
      let url = Persistent.getters.baseUrl + '/api/v2/project/aws/registrationCode/' + certId
      var me = this
      this.$http.post(url, {registrationCode}).then(function (res) {
        if (res.body.status === 200) {
          me.loadProject(me.$route.params.id)
        } else {
          this.notifyError(res.body.status, res.body.error)
        }
      }, response => {
        console.log(response)
        this.notifyError(response.status, response.body.message)
      })
    },
    notifyError: function (code, message) {
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    },
    parseResponse: function (body) {
      this.project = body.project
      this.certificates = body.certificates
      const mqttHook = body.mqtt_hook
      this.cloudOn = mqttHook.enabled
      this.activeCA = mqttHook.activeCA
      if (mqttHook) {
        this.deviceExpire = mqttHook.deviceExpire
        if (mqttHook['AWS_CA']) {
          this.endpoint = mqttHook.AWS_CA.endpoint
          this.certificateId = mqttHook.AWS_CA.certificateId
          if (!this.activeCA && this.certificates.length === 1) {
            this.activeCA = this.certificates[0]._id
          }
        }
      }
    },
    loadProject: function (id) {
      let url = Persistent.getters.baseUrl + '/api/v2/project/' + this.$route.params.id + '/mqtthook'
      this.$http.get(url).then(function (res) {
        if (res.body && res.body.status === 200) {
          this.parseResponse(res.body)
          EventBus.$emit('set-title', this.project.name + '\'s mqtt integration')
        } else {
          this.notifyError(res.body.status, res.body)
        }
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },
    saveWizard: function (redirect) {
      let url = Persistent.getters.baseUrl + '/api/v2/project/' + this.$route.params.id + '/mqtthook'
      this.$http.post(url, { mqtt_hook: this.mqttHook }).then(function (res) {
        if (res.body.status === 200) {
          if (redirect) {
            this.$router.push({
              name: 'ProjectSettings',
              params: { id: this.$route.params.id }
            })
          } else {
            this.project = res.body.project
            this.mqtt_hook = res.body.mqtt_hook
            this.certificates = res.body.certificates
            this.activeCA = this.mqtt_hook.activeCA
            if (this.mqtt_hook['AWS_CA']) {
              this.endpoint = this.mqtt_hook.AWS_CA.endpoint
            }
          }
        } else {
          this.notifyError(res.body.status, res.body.error)
        }
      }, response => {
        console.log(response)
        this.notifyError(response.status, response.body.message)
      })
    },
    downloadAWSPolicyTemplate: async function () {
      if (!this.endpoint) {
        this.notifyError(500, 'endpoint not set')
      }
      const region = this.getAWSRegion()
      if (!region) {
        this.notifyError(500, `cant read region in endpoint: "${this.endpoint}"`)
      }

      const accountId = await this.$refs.registrationCode
        .open('AWS Thing Policy for JITPRole',
          `Please enter your AWS accountId, you can get it from the command
          \`aws iam get-user\`. Please note that we do not store this information.`, {
            color: 'info'
          })
      const content = JSON.stringify(AWS_POLICY_TEMPLATE(accountId, region))
      const filename = `${this.project.slug}-policy-template.json`
      this.download(content, filename)
      // aws iam get-user
    },
    download: function (content, filename, contentType) {
      if (!content) return
      if (!contentType) contentType = 'application/octet-stream'
      var a = document.createElement('a')
      var blob = new Blob([content], {'type': contentType})
      a.href = window.URL.createObjectURL(blob)
      a.download = filename
      a.click()
    }
  }
}
</script>
<style>
.mycode {
  font-size: 16px;
  color: blue!important;
  width: 100%!important;
  padding: 16px!important;
}
</style>
