<template>
      <v-dialog v-model="show" max-width="480">
        <v-form v-model="valid">
        <v-card class="upy-card">
          <v-card-title>
            <h2>Dump file to flash</h2>
          </v-card-title>
          <v-card-text>
            <v-layout wrap>

              <v-flex xs12 v-if="file">
              <v-text-field  v-if="progress==0" :rules="ruleReq"  v-model="path" hint="ex: /flash/file.py" label="path" required></v-text-field>
                <span v-if="progress>0">path: {{path}}<br></span>
                md5: {{md5}}<br>
                size: {{humanFileSize(file.size)}}<br>

              </v-flex>

            <v-flex xs12  text-xs-center v-if="progress === 0">

              <upload-btn text-xs-center  v-if="file === null" color="warning"
                :fileChangedCallback="fileChanged"
              >
              <template slot="icon">
                <v-icon>file_upload</v-icon>
              </template>
              </upload-btn>

            </v-flex>

            <v-flex xs12 text-xs-center  mb-4 mt-2 v-if="progress > 0">

              <h1>{{progress}} % </h1>
              <v-progress-linear v-model="progress"></v-progress-linear>
              <p class="mt-2">{{etaString}}</p>

            </v-flex>

          </v-layout>
          </v-card-text>
        <v-card-actions>
          <v-btn color="danger black--text" raised @click.stop="cancel()">Cancel</v-btn>
          <v-btn color="info black--text" v-if="progress === 0" raised @click.stop="dump()">Dump</v-btn>
          </v-card-actions>
        </v-card>
      </v-form>
   </v-dialog>
</template>

<script>
// import EventBus from '../event-bus.js'
import UploadButton from 'vuetify-upload-button'
import SparkMD5 from 'spark-md5'

export default {
  name: 'BLEDumpFile',
  props: ['show', 'session', 'progress'],
  data () {
    return {
      start: new Date().getTime(),
      valid: false,
      path: '',
      etaString: '',
      file: null,
      ready: false,
      md5: null,
      ruleReq: [
        (v) => !!v || 'field is required'
      ]
    }
  },
  components: {
    'upload-btn': UploadButton
  },
  watch: {
    progress: function (val) {
      this.eta()
    }
  },
  methods: {
    eta: function () {
      if (this.progress === 1) {
        this.start = new Date().getTime()
      }
      const now = new Date().getTime()
      const elapsed = now - this.start
      const duration = Math.ceil(elapsed * 100 / this.progress)
      const seconds = Math.ceil((now - this.start - duration) / -1000)
      const minutes = Math.floor(seconds / 60)
      const s = seconds - minutes * 60
      if (seconds === 0) {
        return
      }
      if (minutes === 0) {
        this.etaString = `${seconds} secondsd remaining`
      } else {
        this.etaString = `${minutes} minutes ${s} seconds remaining`
      }
    },
    humanFileSize (size) {
      var i = Math.floor(Math.log(size) / Math.log(1024))
      return (size / Math.pow(1024, i)).toFixed(2) * 1 + ' ' + ['B', 'kB', 'MB', 'GB', 'TB'][i]
    },
    fileChanged (file) {
      let me = this
      this.file = file
      // this.lastModified = file.lastModifiedDate
      this.path = '/flash/' + file.name
      let reader = new FileReader()
      reader.onloadend = function (e) {
        me.data = btoa(e.target.result)
        var spark = new SparkMD5()
        spark.appendBinary(e.target.result)
        me.md5 = spark.end()
      }
      // reader.readAsArrayBuffer(file)
      reader.readAsBinaryString(file)
    },
    dump () {
      const message = `POST file\nmd5:${this.md5}\npath:${this.path}\naccess_token:${this.session}\n\n${this.data}`
      this.$emit('onChunk', message)
      // this.file = null
    },
    cancel () {
      this.file = null
      this.data = 0
      this.$emit('onClose')
    }

  }
}
</script>
