<template>
      <v-dialog width=640 v-model="dialog">
        <v-card class="upy-card">
          <v-card-title v-if="device.application && device.application.name">
            device is using application {{device.application.name}}
          </v-card-title>
          <v-card-title v-else>
            {{device.name}} has no application
          </v-card-title>
          <v-card-text>
            <p v-if="applications.length === 0" class="text-xs-center">
              You have no application yet,<br>
              <v-btn @click="navigate('Applications')" center>Manage applications</v-btn>
            </p>
            <p v-if="device.application && device.application._id" class="text-xs-center">
              <v-btn
                @click="navigate('ApplicationSettings', device.application._id )"
                center>Manage application {{device.application.name}}
              </v-btn>
            </p>

            <v-list subheader two-line v-if="applications.length > 0 && (!device.application || !device.application._id)">
            <v-subheader>please select an application below using the button</v-subheader>

                <template  v-for="(application, index) in applications">
                  <v-list-tile  :key="index + 'ws'"  class="mr-3">

                      <v-list-tile-content>
                          <v-list-tile-title>{{application.name}}</v-list-tile-title>
                          <v-list-tile-sub-title>{{application.slug}}</v-list-tile-sub-title>
                      </v-list-tile-content>

                      <v-list-tile-action>
                        <v-btn  class="info" @click.prevent="setApplication(application._id)">
                        <v-icon>touch_app</v-icon>
                      </v-btn>
                      </v-list-tile-action>
                  </v-list-tile>
                  <v-divider :key="index" class="mr-3"></v-divider>
                </template>
            </v-list>
          </v-card-text>
          <v-card-actions>
            <v-btn v-if="device.application && device.application._id"
            flat @click.native="removeApplication()"
            class="warning black--text">REMOVE APPLICATION</v-btn>
            <v-spacer/>
            <v-btn  class="secondary" flat @click.native="dialog= false">
                Close
              </v-btn>
          </v-card-actions>
      </v-card>
          <v-confirm ref="confirm"></v-confirm>
        </v-dialog>
</template>

<script>
import Events from '../../store/event-api.js'
import EventBus from '../../event-bus.js'
import Persistent from '../../store/persistent'
import DialogConfirm from '@/components/global/DialogConfirm'

export default {
  name: 'DeviceApplications',
  props: ['dialogSignal'],
  data: function () {
    return {
      dialog: false,
      device: {},
      applications: []
    }
  },
  components: {
    'v-confirm': DialogConfirm
  },
  watch: {
    dialogSignal: function (val) {
      this.dialog = !this.dialog
    }
  },
  created () {
    this.loadDevice(this.$route.params.id)
    this.loadApplications()
  },
  methods: {
    navigate: function (component, id) {
      this.$router.push({
        name: component,
        params: {
          id: id
        }
      })
    },
    loadApplications: function () {
      let url = Persistent.getters.baseUrl + '/api/v2/applications'
      this.$http.get(url).then(function (res) {
        if (res.data && res.data.applications) {
          this.applications = res.data.applications
        }
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },
    loadDevice: function () {
      let url = Persistent.getters.baseUrl + '/api/v2/account/device/' + this.$route.params.id
      this.$http.get(url).then(function (res) {
        if (res.data && res.data.device) {
          this.device = res.data.device
        }
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },
    setApplication: async function (id) {
      if (!await this.$refs.confirm.open('Change application assigned', 'Do you confirm?', {
        color: 'info'
      })) {
        return
      }
      // POST /api/v2/application/APPLICATION_SLUG/:DEVICE_ID, assign device to application
      let url = Persistent.getters.baseUrl + '/api/v2/application/' + id + '/device/' + this.$route.params.id
      this.$http.post(url).then(function (res) {
        if (res.body && res.body.status === 200) {
          this.loadDevice()
          this.$emit('update')
        } else {
          this.notifyError(res.body.status, res.body.error)
        }
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },
    removeApplication: async function () {
      if (!await this.$refs.confirm.open('Delete application assignement', 'Do you confirm?', {
        color: 'info'
      })) {
        return
      }
      let url = Persistent.getters.baseUrl + '/api/v2/account/device/' + this.$route.params.id + '/application'
      this.$http.delete(url).then(function (res) {
        this.loadDevice()
        this.$emit('update')
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },

    notifyError: function (code, message) {
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    }
  }
}
</script>
