<template>
  <v-layout row wrap v-if="message">
    <v-flex xs12 mt-3 mb-0 pa-0>
      <p class="yellow--text text--darken-1 text-xs-center">
        {{ new Date(message.ts).toLocaleString()}}
      </p>
    </v-flex>
    <v-flex v-bind:key='index' v-for="(gauge, index) in gauges"  mb-1 md4 sm6 xs12 pa-1>
      <v-card  v-bind:key="index" class="info ma-2 elevation-4">
        <v-card-title class="mb-0 pb-0">
        {{ gauge.key }}
      </v-card-title>
      <v-card-text class="text-xs-center mt-0 pt-0">
        <v-segment-display :number="gauge.value"/>
      <v-layout row class="fab-container">
          <v-btn fab small>
            <v-icon>build</v-icon>
          </v-btn>
        </v-layout>
      </v-card-text>
    </v-card>
<!--  </template> -->
</v-flex>
</v-layout>
</template>

<script>
import SegmentDisplay from './widgets/SegmentDisplay'

export default {
  props: ['message'],
  data: () => ({
    keys: [],
    values: [],
    gauges: []
  }),
  components: {
    'v-segment-display': SegmentDisplay
  },

  watch: {
    active: function (val) {
      console.log('active')
    },
    message: function (val) {
      if (!val) return
      if (!val.data) return
      this.gauges = []
      for (var prop in val.data) {
        if (!isNaN(val.data[prop])) {
          this.gauges.push({ key: prop, value: val.data[prop] })
        }
      }
    }
  }
}
</script>
<style>
.fab-container {
  position: absolute;
  bottom: 0;
  right: 0;
}
</style>
