<template>
  <v-container fill-height justify-center  justify-start  fluid grid-list-sm>
    <v-layout  align-start wrap>
      <v-flex xs12>
        <v-card class="white">
            <v-device-card-title :device="device" :version="version">
            </v-device-card-title>
            <v-card-text>
              <json-editor ref="editor" :onChange="onConfigChange" :json="attributes" />
            </v-card-text>
            <v-layout   absolute bottom right>
              <v-btn  fab   color="default" @click.native="navigate('DeviceSettings')">
                <v-icon>close</v-icon>
              </v-btn>
              <v-btn  fab   color="primary" @click.native="saveDevice()">
                <v-icon>done</v-icon>
              </v-btn>
             </v-layout>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import JSONEditor from 'vue2-jsoneditor'
import Events from '../../store/event-api.js'
import EventBus from '../../event-bus.js'
import Persistent from '../../store/persistent'
import DeviceSettingsCardTitle from './DeviceSettingsCardTitle.vue'

export default {
  name: 'DeviceAttributes',
  components: {
    'json-editor': JSONEditor,
    'v-device-card-title': DeviceSettingsCardTitle
  },
  data: function () {
    return {
      device: {},
      attributes: null,
      output: null,
      mode: 'tree',
      version: null
    }
  },
  created () {
    this.loadDevice(this.$route.params.id)
  },
  mounted () {
    EventBus.$emit('set-title', 'Attributes device settings')
    const editor = this.$refs.editor.editor
    editor.setMode(this.mode)
  },
  methods: {
    onConfigChange: function (c) {
      this.output = Object.assign({}, c)
    },
    saveDevice: function () {
      let url = Persistent.getters.baseUrl + '/api/v2/account/device/' + this.$route.params.id + '/attributes'
      let data = Object.assign({}, this.output)
      this.$http.post(url, data).then(function (res) {
        this.$router.push({
          name: 'DeviceSettings',
          params: { id: this.$route.params.id }
        })
      }, response => {
        console.log(response)
        console.log('error here')
        this.notifyError(response.status, response.body.message)
      })
    },
    loadDevice: function () {
      let url = Persistent.getters.baseUrl + '/api/v2/account/device/' + this.$route.params.id + '/attributes'
      let me = this
      this.$http.get(url).then(function (res) {
        if (res.data.device) {
          this.device = res.data.device
          EventBus.$emit('set-title', this.device.name + ' advanced settings')
          if (res.data && res.data.attributes) {
            this.attributes = res.data.attributes
            this.output = res.data.attributes
            this.version = res.data.version
            const editor = me.$refs.editor.editor
            setTimeout(function () {
              editor.expandAll()
            }, 1)
          } else {
            // this is a 500 error, server shall have served an clean new config
            this.attributes = {}
            this.notifyError(500, 'an empty attributes has been created')
          }
        }
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },
    notifyError: function (code, message) {
      console.log(Events.apiError, code, message)
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    },
    navigate: function (component) {
      this.$router.push({
        name: component,
        params: {
          id: this.device._id
        }
      })
    }
  }
}
</script>

