<template>
      <v-dialog width=640 v-model="dialog">
        <v-card class="upy-card">
          <v-card-title>
            Add API KEy
          </v-card-title>
          <v-card-text>
            <v-form ref="form">
              <v-layout row wrap>
                <v-flex md4 xs12>
                  <v-text-field
                    hint="Descriptions"
                    v-model="api_descriptions"
                    label="Descriptions"
                  ></v-text-field>
                </v-flex>
              </v-layout>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer/>
            <v-btn  class="secondary" flat @click.native="dialog= false">
                Close
              </v-btn>
              <v-btn class="primary" flat @click="addAPIKey()">
                Save
              </v-btn>
          </v-card-actions>
        </v-card>
        <v-confirm ref="confirm"></v-confirm>
      </v-dialog>
</template>

<script>
import Events from '../../store/event-api.js'
import EventBus from '../../event-bus.js'
import Persistent from '../../store/persistent'
import DialogConfirm from '@/components/global/DialogConfirm'

export default {
  name: 'APIKey',
  props: ['dialogSignal'],
  data: function () {
    return {
      dialog: false,
      api_descriptions: null
    }
  },
  components: {
    'v-confirm': DialogConfirm
  },
  watch: {
    dialogSignal: function (val) {
      this.dialog = !this.dialog
    }
  },
  mounted () {
  },
  methods: {
    addAPIKey () {
      let url = Persistent.getters.baseUrl + '/api/v2/api-keys'
      let data = { description: this.api_descriptions }
      this.$http.put(url, data).then(function (res) {
        this.$emit('update')
        this.dialog = false
      }, response => {
        console.log(response)
        console.log('error here')
        this.notifyError(response.status, response.body.message)
      })
    },
    navigate: function (component, id) {
      this.$router.push({
        name: component,
        params: {
          id: id
        }
      })
    },
    notifyError: function (code, message) {
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    }
  }
}
</script>
