<script>
import Events from '../../store/event-api.js'
import Persistent from '../../store/persistent'
import DialogConfirm from '@/components/global/DialogConfirm'
import DialogName from '@/components/global/DialogName'
import EventBus from '../../event-bus.js'
import MQTTSettings from '../global/MQTTSettings'

export default {
  name: 'Project',
  data () {
    return {
      user: Persistent.getters.user,
      project: {configuration: {data: {}}},
      version: 0
    }
  },
  components: {
    'v-confirm': DialogConfirm,
    'v-mqtt': MQTTSettings,
    'v-dialog-name': DialogName
  },
  created () {
    this.loadProject(this.$route.params.id)
  },
  mounted () {
    EventBus.$emit('set-title', 'Project settings')
  },
  methods: {
    mqttEdit: function (mqtt) {
      console.log('parent got edit', mqtt)
      this.project.mqtt = mqtt
    },
    saveProject: function () {
      let url = Persistent.getters.baseUrl + '/api/v2/project/' + this.project._id
      let data = this.project
      this.$http.post(url, data).then(function (res) {
        this.$router.push({
          name: 'Projects'
        })
      }, response => {
        this.notifyError(response.status, response.body.message)
      })
    },
    loadProject: function (id) {
      let url = Persistent.getters.baseUrl + '/api/v2/project/' + id
      this.$http.get(url).then(function (res) {
        if (res.body.status === 200) {
          if (res.data && res.data.project) {
            this.project = res.data.project
          }
        } else {
          this.notifyError(res.body.status, res.body.error)
        }
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },
    duplicateProject: async function (id) {
      const name = await this.$refs.dialogName.open('Duplicate project', 'Please select new project name', {
        color: 'info'
      })
      let url = Persistent.getters.baseUrl + '/api/v2/project/' + this.project._id + '/duplicate/' + name
      this.$http.get(url).then(function (res) {
        this.$router.push({
          name: 'Projects'
        })
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },
    deleteProject: async function (id) {
      if (await this.$refs.confirm.open('Delete project', 'Are you sure?', {
        color: 'warning'
      })) {
        let url = Persistent.getters.baseUrl + '/api/v2/project/' + this.project._id
        this.$http.delete(url).then(function (res) {
          this.$router.push({
            name: 'Projects'
          })
        }, response => {
          this.notifyError(response.status, response.statusText)
        })
      }
    },
    notifyError: function (code, message) {
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    },
    navigate: function (component) {
      this.$router.push({
        name: component,
        params: {
          id: this.project._id,
          project: this.project
        }
      })
    }
  }
}
</script>

