<template>
  <v-card class="upy-card flexcard">
    <v-card-title class="applications">Base firmware</v-card-title>
      <v-card-text class="grow">
        <p>You can upload a firmware build or select a branch to build your
        application.</p>
        <v-text-field
          full-width
          label="git branch to build"
          v-model="gitUrl">
        </v-text-field>


        <p class="text-xs-center">

          </p>

        </v-card-text>
        <v-card-actions>

          <upload-btn uniqueId primary :fileChangedCallback="fwUpload">
            <template slot="icon">
            <v-icon class="ml-2">cloud_upload</v-icon>
            </template>
          </upload-btn>

          <v-btn disabled flat class="warning" @click="toggle_upload = true">Make ...</v-btn>
        </v-card-actions>
        <v-confirm ref="confirm"></v-confirm>
    </v-card>
</template>

<script>
import Persistent from '../../store/persistent'
import SelectBoard from './SelectBoard'
import UploadButton from 'vuetify-upload-button'
// import EventBus from '../../event-bus.js'
// import Events from '../../store/event-api.js'
import DialogConfirm from '@/components/global/DialogConfirm'

export default {
  props: ['application'],
  components: {
    'v-confirm': DialogConfirm,
    'v-select-board': SelectBoard,
    'upload-btn': UploadButton
  },
  data: () => ({

  }),
  computed: {
    gitUrl: () => {
      if (this.application && this.application.firmware && this.application.firmware.git_url) {
        return this.application.firmware.git_url
      } else {
        return null // https://github.com/micropython/micropython/tree/v1.11
      }
    }
  },
  methods: {
    deleteFw: async function (fw) {
      if (await this.$refs.confirm.open('Delete firmware ',
        `Are you sure you want to delete ${fw.filename} from the server ?`, {
          color: 'warning'
        })) {
        const applicationId = this.application._id
        const url = `${Persistent.getters.baseUrl}/api/v2/application/${applicationId}/firmware/${fw.board}/${fw._id}`
        this.$http.delete(url).then(function () {
          this.$emit('update')
        }).catch(function (e) {
          this.notifyError(e.status, e.statusText)
          this.$emit('update')
        })
      }
    },
    fwUpload: function (file) {
      const applicationId = this.application._id
      let url = `${Persistent.getters.baseUrl}/api/v2/application/${applicationId}/firmware/${this.board}`
      let formData = new FormData()
      formData.append('file', file)
      this.$http.put(url, formData, {headers: {'Content-Type': 'multipart/form-data'}})
        .then(function () {
          this.$emit('update')
          this.toggle_upload = false
        })
        .catch(function (e) {
          this.notifyError(e.status, e.statusText)
        })
    }
  }
}
</script>
