<template>
  <v-container fluid grid-list-md ma-0 pa-0 v-if="device">
    <v-layout align-content-center row wrap >
      <v-flex xs12>

        <v-navigation-drawer
        permanent
        v-model="drawerRight"
        fixed
        right
        app>
        <v-list avatar three-line>

          <v-subheader>{{device.name}}</v-subheader>

          <v-list-tile
            avatar
          >
          <v-list-tile-action>
            <v-icon color="indigo">location_off</v-icon>
          </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title >
                  No data
              </v-list-tile-title>
            </v-list-tile-content>
            <v-list-tile-action>
              <v-btn icon ripple @click.stop="navigate('DeviceSettings', device._id)">
                <v-icon color="grey lighten-1">edit</v-icon>
              </v-btn>
            </v-list-tile-action>

          </v-list-tile>



            <template v-for="item in messages">
        <v-list-tile
          :key="item.title"
          avatar
          @click="navigate('DeviceData', item._id)"
        >
        <v-list-tile-action>
          <v-icon color="indigo">nfc</v-icon>
        </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title >
                {{ item.name }}
            </v-list-tile-title>
              <v-list-tile-sub-title v-html="item.unique_id"></v-list-tile-sub-title>

          </v-list-tile-content>
          <v-list-tile-action>
            <v-btn icon ripple>
              <v-icon color="grey lighten-1">info</v-icon>
            </v-btn>
          </v-list-tile-action>

        </v-list-tile>
        </template>
        </v-list>
        </v-navigation-drawer>
        <v-card class="white ma-4 ml-1" style="height: 100%;">

            <l-map
              ref="mapref"
              style="height: 505px; width: calc(100% + 0px);"
              :zoom="zoom"
            >
              <l-tile-layer :url="url" :attribution="attribution"></l-tile-layer>
            </l-map>
        </v-card>
      </v-flex>
</v-layout>
</v-container>
</template>

<script>
import Events from '../../store/event-api.js'
import Persistent from '../../store/persistent'
import EventBus from '../../event-bus.js'
// import L from 'leaflet'
import { LMap, LTileLayer, LMarker, LTooltip } from 'vue2-leaflet'

export default {
  name: 'DeviceData',
  data () {
    return {
      zoom: 1,
      user: Persistent.getters.user,
      device: null,
      messages: [],
      message: {},
      drawerRight: true,
      pagination: {'sortBy': 'ts', 'descending': true, 'rowsPerPage': 10},
      url: 'http://{s}.tile.osm.org/{z}/{x}/{y}.png',
      attribution: '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
    }
  },
  components: {
    LMap,
    LTileLayer,
    LMarker,
    LTooltip
  },
  mounted () {
    EventBus.$emit('set-title', 'data')
  },
  created () {
    this.loadDevice(this.$route.params.id)
  },
  beforeDestroy () {
    if (this.client) {
      this.client.end()
    }
  },
  watch: {
    pagination: {
      handler (v) {
        this.stream = !this.stream
        if (v.page === 1) {
          this.stream = true
        } else {
          this.stream = false
        }
      },
      deep: true
    }
  },
  methods: {
    navigate: function (name, id) {
      this.$router.push({
        name,
        params: {
          id
        }
      })
    },
    init () {
      this.getBefore()
    },
    onState (s) {
      this.stream = s
    },
    onData (topic, message) {
      const now = parseInt(new Date().getTime() / 1000, 10)
      this.getAfter(now, 100)
    },
    fillTop (data) {
      const lastTime = this.messages[0] ? this.messages[0].ts : 0
      const messages = this.messages
      for (let i = 0; i < data.length; i++) {
        if (data[i].ts <= lastTime) {
          break
        }
        messages.unshift(Object.assign({}, {
          topic: data[i].topic,
          ts: data[i].ts,
          data: data[i].data
        }))
        if (messages.length > this.MAX_STACK) {
          messages.pop()
        }
      }
      this.messages = messages
      this.message = messages[0]
    },
    parseData (data) {
      let messages = []
      for (let i = 0; i < data.length; i++) {
        messages.push(Object.assign({}, {
          topic: data[i].topic, // .split('/')[3],
          ts: data[i].ts,
          data: data[i].data
        }))
      }
      this.messages = messages
      this.message = this.messages[0]
    },
    getAfter (start, limit = 100) {
      const id = this.device._id
      let url = Persistent.getters.baseUrl + '/api/v2/data/device/' + id + '/after/' + start + '/' + limit
      this.$http.get(url).then(function (res) {
        if (res.data.error) {
          this.notifyError(500, res.data.error.errmsg)
          this.client.end()
          return
        }
        this.fillTop(res.data.messages)
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },
    getBefore (start = parseInt(new Date().getTime() / 1000), limit = 100) {
      const id = this.device._id
      let url = Persistent.getters.baseUrl + '/api/v2/data/device/' + id + '/before/' + start + '/' + limit
      this.$http.get(url).then(function (res) {
        if (res.data.error) {
          this.notifyError(500, res.data.error.errmsg)
          this.client.end()
          return
        }
        this.parseData(res.data.messages)
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },
    loadDevice: function (id) {
      let url = Persistent.getters.baseUrl + '/api/v2/account/device/' + id
      this.$http.get(url).then(function (res) {
        if (res.data && res.data.device && res.data.device._id) {
          this.device = res.data.device
          this.client = this.init()
          EventBus.$emit('set-title', ' path')
          // this.update_on_boot = this.device.configuration.update_on_boot
        } else {
          this.notifyError(500, 'unexpected error DeviceSettings.loadDevice')
          if (this.client) {
            this.client.end()
          }
          this.$router.push({name: 'Devices'})
        }
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },
    notifyError: function (code, message) {
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    },
    notifySuccess: function (code, message) {
      EventBus.$emit(Events.apiSuccess, code, message)
    },
    back: function () {
      this.$router.push({
        name: 'Devices'
      })
    }
  }
}

</script>
