
<template>
  <v-layout>
      <v-flex mt-4 xs12 offset-md2 md8>
          <v-card class="upy-card" mt-2>
            <v-card-text pb-4 class="pb-5">
            <v-list subheader two-line>
                <v-subheader class="applications">Applications</v-subheader>
                <template  v-for="(application, index) in applications">
                  <span :key="index">
                  <v-divider  class="mr-3"></v-divider>
                  <v-list-tile  @click="openApplication(application._id)" class="mr-3">
                      <v-list-tile-content>
                          <v-list-tile-title>{{application.name}}</v-list-tile-title>
                          <v-list-tile-sub-title>{{application.slug}}</v-list-tile-sub-title>
                      </v-list-tile-content>
                      <v-list-tile-action>
                        <v-icon>settings</v-icon>
                      </v-list-tile-action>
                  </v-list-tile>
                </span>
              </template>
            </v-list>
          </v-card-text>

          </v-card>
          <v-btn  fixed right bottom fab @click="addApplication" color="info">
              <v-icon>add</v-icon>
          </v-btn>
      </v-flex>
      <v-dialog-name ref="application"></v-dialog-name>
  </v-layout>
</template>

<script>

import EventBus from '../../event-bus.js'
import Events from '../../store/event-api.js'
import Persistent from '../../store/persistent'
import DialogName from '@/components/global/DialogName'

export default {
  name: 'Applications',
  props: ['config'],
  data () {
    return {
      user: Persistent.getters.user,
      applications: []
    }
  },
  components: {
    'v-dialog-name': DialogName
  },
  created () {
    this.loadApplications()
  },
  mounted () {
    EventBus.$emit('set-title', 'manage applications')
  },
  methods: {
    openApplication: function (id) {
      this.$router.push({
        name: 'ApplicationSettings',
        params: {
          id: id
        }
      })
    },
    addApplication: async function () {
      const name = await this.$refs.application.open('Add application', 'Please set a application name', {
        color: 'info'
      })
      if (!name) {
        this.notifyError(400, 'application name is required')
        return
      }
      let url = Persistent.getters.baseUrl + '/api/v2/applications'
      this.$http.put(url, {name}).then(function (res) {
        if (res.data.status === 200 && res.data.application._id) {
          this.$router.push({name: 'ApplicationSettings', params: { id: res.data.application._id }})
        } else {
          console.log(res.data)
          this.notifyError(res.body.status, res.body.error)
        }
      }, response => {
        console.log(response)
        this.notifyError(response.status, response.body.message)
      })
    },
    loadApplications: function () {
      let url = Persistent.getters.baseUrl + '/api/v2/applications'
      this.$http.get(url).then(function (res) {
        if (res.body.data !== null) {
          this.applications = res.body.applications
        }
      }, response => {
        console.log(response)
        this.notifyError(response.status, response.body.message)
      })
    },
    notifyError: function (code, message) {
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    }
  }

}

</script>
