<template>
  <v-container fill-height justify-center  justify-start  fluid grid-list-sm>
    <v-layout  v-if="device" align-start wrap>
      <v-flex xs12 offset-md2 md8>
        <v-card class="upy-card">
          <v-device-card-title :device="device" :version="version"></v-device-card-title>
            <v-card-text>
              <v-layout row wrap pa-4>
                <v-flex md4 pt-4>
                  <v-switch v-model="ble_app_on_boot" label="BLE update application on boot"></v-switch>
                </v-flex>
                <v-flex md6 pa-4>
                  <v-text-field type="number" label="Pincode"  v-model="pincode"></v-text-field>
                </v-flex>
                <v-flex md2 pa-4>
                  <v-text-field type="number" label="await time"  v-model="await_time"></v-text-field>
                </v-flex>
            </v-layout>
          </v-card-text>
        <v-btn absolute fab bottom right color="primary" @click.native="saveBLE()">
          <v-icon>done</v-icon>
        </v-btn>
      </v-card>
    </v-flex>
  </v-layout>
</v-container>
</template>

<script>
import Events from '../../../store/event-api.js'
import EventBus from '../../../event-bus.js'
import Persistent from '../../../store/persistent'
import DeviceSettingsCardTitle from '../DeviceSettingsCardTitle.vue'

export default {
  name: 'DeviceBLESettings',
  props: ['configuration', 'device', 'version'],
  data: function () {
    return {
      pincode: null,
      await_time: 60,
      ble_app_on_boot: true
    }
  },
  components: {
    'v-device-card-title': DeviceSettingsCardTitle
  },
  mounted () {
    EventBus.$emit('set-title', 'Device BLE settings')
    if (this.configuration) {
      const ble = this.configuration.ble || {}
      this.pincode = ble.pincode
      this.await_time = ble.await_time || 60
      this.ble_app_on_boot = this.ble_app_on_boot || true
    } else {
      EventBus.$emit(Events.loadingEnd)
      // redirect to device settings home
      this.$router.push({
        name: 'DeviceSettings',
        params: { id: this.$route.params.id }
      })
    }
  },
  methods: {
    saveBLE: function () {
      // finally post datas ///
      const ble = { pincode: this.pincode, await_time: this.await_time, ble_app_on_boot: this.ble_app_on_boot }
      let url = Persistent.getters.baseUrl + '/api/v2/account/device/' + this.$route.params.id + '/configuration'
      this.$http.post(url, { ble }).then(function (res) {
        if (res.status === 200) {
          // redirect to device settings home
          this.$router.push({
            name: 'DeviceSettings',
            params: { id: this.$route.params.id }
          })
        } else {
          this.notifyError(res.body.status, res.body.error)
        }
      }, response => {
        console.log('error updating LTE config:', response)
        this.notifyError(response.status, response.body.message)
      })
    },
    notifyError: function (code, message) {
      console.log(Events.apiError, code, message)
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    }
  }
}
</script>
