<script>
import Events from '../../store/event-api.js'
import Persistent from '../../store/persistent'
import DialogConfirm from '@/components/global/DialogConfirm'
import DialogName from '@/components/global/DialogName'
import DeviceApplication from '@/components/devices/DeviceApplication'
import DeviceProject from '@/components/devices/DeviceProject'
import EventBus from '../../event-bus.js'

export default {
  name: 'Device',
  data () {
    return {
      user: Persistent.getters.user,
      device: {configuration: {data: {}}, access_tokens: []},
      update_on_boot: false,
      version: 0,
      applications: [],
      projects: [],
      mqtt: {},
      dialogApp: false,
      dialogProject: false,
      mqtt_platform_defaults: {}
    }
  },
  components: {
    'v-confirm': DialogConfirm,
    'v-dialog-name': DialogName,
    'v-device-application': DeviceApplication,
    'v-device-project': DeviceProject
  },
  created () {
    this.loadDevice(this.$route.params.id)
  },
  mounted () {
    EventBus.$emit('set-title', 'device settings')
    let me = this
    const url = Persistent.getters.baseUrl + '/api/v2/applications/mqtt_platform_defaults'
    this.$http.get(url).then(function (res) {
      if (res.data && res.data.mqtt_platform_defaults) {
        me.mqtt_platform_defaults = res.data.mqtt_platform_defaults
      } else {
        this.notifyError(500, 'unable to get mqtt platform defaults')
      }
    }, response => {
      this.notifyError(response.status, response.statusText)
    })
  },
  computed: {
    show_mqtt_settings: function () {
      return false
      // return (this.device.application && this.device.application.mqtt &&
      //   this.mqtt_platform_defaults.server !== this.device.application.mqtt.server) ||
      // (this.device.project && this.device.project.mqtt &&
      // this.mqtt_platform_defaults.server !== this.device.project.mqtt.server)
    }
  },
  methods: {
    saveUpdateOnBoot: function (value) {
      let url = Persistent.getters.baseUrl + '/api/v2/account/device/' + this.$route.params.id + '/configuration'
      const updates = this.device.configuration.data
      this.$http.post(url, updates).then(function (res) {
        if (res.status === 200) {
          // redirect to device settings home
          this.$router.push({
            name: 'DeviceSettings',
            params: { id: this.$route.params.id }
          })
        } else {
          this.notifyError(res.body.status, res.body.error)
        }
      }, response => {
        console.log('error updating LTE config:', response)
        this.notifyError(response.status, response.body.message)
      })
    },
    saveDevice: function () {
      let url = Persistent.getters.baseUrl + '/api/v2/account/device/' + this.device._id
      let data = this.device
      this.$http.post(url, data).then(function (res) {
        this.$router.push({
          name: 'Devices'
        })
      }, response => {
        console.log(response)
        console.log('error here')
        this.notifyError(response.status, response.body.message)
      })
    },
    loadDevice: function (id) {
      let url = Persistent.getters.baseUrl + '/api/v2/account/device/' + id
      this.$http.get(url).then(function (res) {
        if (res.data && res.data.device && res.data.device._id) {
          this.device = res.data.device
          EventBus.$emit('set-title', 'device settings')
          // this.update_on_boot = this.device.configuration.update_on_boot
        } else {
          this.notifyError(500, 'unexpected error DeviceSettings.loadDevice')
          this.$router.push({name: 'Devices'})
        }
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },
    duplicateDevice: async function (id) {
      const name = await this.$refs.dialogName.open('Duplicate device', 'Please select new device name', {
        color: 'info'
      })
      let url = Persistent.getters.baseUrl + '/api/v2/account/device/' + this.device._id + '/duplicate/' + name
      this.$http.get(url).then(function (res) {
        this.$router.push({
          name: 'Devices'
        })
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },
    deleteDevice: async function (id) {
      if (await this.$refs.confirm.open('Delete device', 'Are you sure?', {
        color: 'warning'
      })) {
        let url = Persistent.getters.baseUrl + '/api/v2/account/device/' + this.device._id
        this.$http.delete(url).then(function (res) {
          this.$router.push({
            name: 'Devices'
          })
        }, response => {
          this.notifyError(response.status, response.statusText)
        })
      }
    },
    notifyError: function (code, message) {
      console.log(Events.apiError, code, message)
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    },
    openDialog: function (component) {
      if (component === 'DeviceApplication') {
        this.dialogApp = !this.dialogApp
      }
      if (component === 'DeviceProject') {
        this.dialogProject = !this.dialogProject
      }
    },
    navigate: function (component) {
      this.$router.push({
        name: component,
        params: {
          id: this.device._id,
          configuration: this.device.configuration.data,
          device: this.device,
          version: this.version
        }
      })
    },
    saveLocation: function () {
      console.log('test', this)
    }
  }
}
</script>
