<template>
  <v-layout>
    <v-flex xs12 ma-2 pt-4 pb-4>
      <v-progress-linear
        v-if="progressValue > 0"
        v-model="progressValue"
        color="orange">
         </v-progress-linear>
    </v-flex>
  </v-layout>
</template>

<script>

export default {
  name: 'ApplyFlash',
  data: function () {
    return {
      message: '',
      progressValue: 0,
      status: 'disconnected'
    }
  },
  mounted () {
    this.connect()
  },
  methods: {
    connect () {
      this.socket = new WebSocket('ws://127.0.0.1:8765')
      this.socket.onopen = () => {
        this.status = 'connected'
        this.socket.onmessage = ({data}) => {
          this.progressValue = JSON.parse(data).progressValue
        }
      }
    },
    disconnect () {
      this.socket.close()
      this.status = 'disconnected'
      this.logs = []
    },
    sendMessage (e) {
      this.socket.send(this.message)
      this.logs.push({ event: 'Sent message', data: this.message })
      this.message = ''
    }
  }
}
</script>
