<template>
  <v-container fill-height justify-center justify-start fluid grid-list-sm>
    <v-layout v-if="device" align-start wrap>
      <v-flex xs12 offset-md2 md8>
        <v-card class="upy-card">
          <v-device-card-title :device="device" :version="version"></v-device-card-title>
          <v-card-text>
            <v-layout row wrap pa-4>
              <v-flex md4>
                <v-checkbox v-model="abp" label="Activation by personalization (ABP)"></v-checkbox>
              </v-flex>
              <v-flex md4>
                <v-checkbox v-model="otaa" label="Activation over the air (OTAA)"></v-checkbox>
              </v-flex>
              <v-flex md4>
                <v-checkbox v-model="nano" label="Make nano-gateway config"></v-checkbox>
              </v-flex>
              <v-flex md4 xs12>
                <v-select :items="regions" label="region" v-model="region"></v-select>
              </v-flex>
              <v-flex md4 xs12 v-if="otaa">
                <v-select :items="data_rates" label="data rate" v-model="data_rate"></v-select>
              </v-flex>
            </v-layout>
            <v-form ref="form" v-model="valid">
              <v-layout row wrap pa-4 v-if="otaa">
                <v-flex md4 xs12>
                  <v-text-field
                    maxlength="16"
                    hint="Hex"
                    v-model="otaa_dev_eui"
                    label="Device EUI OTAA"
                    :rules="devEuiRules"
                  ></v-text-field>
                </v-flex>
                <v-flex md4 xs12>
                  <v-text-field
                    maxlength="32"
                    hint="Hex"
                    v-model="otaa_nwk_key"
                    label="Network key OTAA"
                    :rules="nwkKeyRules"
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap pa-4 v-if="abp">
                <v-flex md4 xs12>
                  <v-text-field
                    maxlength="16"
                    hint="Hex"
                    v-model="abp_dev_eui"
                    label="Device EUI ABP"
                    :rules="devEuiRules"
                  ></v-text-field>
                </v-flex>
                <v-flex md4 xs12>
                  <v-text-field
                    maxlength="32"
                    hint="Hex"
                    v-model="abp_nwk_key"
                    label="Network key ABP"
                    :rules="nwkKeyRules"
                  ></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap pa-4 v-if="nano">
                <v-flex md4 xs12>
                  <v-text-field
                    hint="IP Address"
                    v-model="server"
                    label="server IP"
                  ></v-text-field>
                </v-flex>
                <v-flex md4 xs12>
                  <v-text-field
                    hint="Port Number"
                    v-model="port"
                    label="Port Number"
                  ></v-text-field>
                </v-flex>
                <v-flex md4 xs12>
                  <v-text-field
                    hint="Gateway ID"
                    v-model="gateway_id"
                    label="Gateway ID"
                    :rules="gatewayRules"
                  ></v-text-field>
                </v-flex>
              </v-layout>
            </v-form>
          </v-card-text>
          <v-layout absolute bottom right>
            <v-btn fab color="default" @click.native="navigate('DeviceSettings')">
              <v-icon>close</v-icon>
            </v-btn>
            <v-btn fab color="primary" @click.native="save()">
              <v-icon>done</v-icon>
            </v-btn>
          </v-layout>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import Events from '../../../store/event-api.js'
import EventBus from '../../../event-bus.js'
import Persistent from '../../../store/persistent'
import DeviceSettingsCardTitle from '../DeviceSettingsCardTitle.vue'

export default {
  name: 'DeviceLoRaSettings',
  props: ['configuration', 'device', 'version'],
  data: function () {
    return {
      regions: ['AS923', 'AU915', 'EU868', 'US915'],
      data_rates: [1, 2, 3, 4, 5],
      region: null,
      data_rate: null,
      channel: null,
      band: null,
      abp: false, // usefull for toggle pannel
      otaa: true, // idem
      nano: false,
      valid: false,
      abp_dev_eui: null,
      abp_nwk_key: null,
      otaa_dev_eui: null,
      otaa_nwk_key: null,
      server: null,
      port: null,
      gateway_id: null,
      re1: /[0-9A-Fa-f]{6}/g,
      /* eslint-disable no-mixed-operators */
      nwkKeyRules: [
        v => !!v || 'Network key is required',
        v => v && v.match(/[0-9A-Fa-f]{32}/g) !== null || 'Network key must be valid'
      ],
      devEuiRules: [
        v => !!v || 'Dev EUI is required',
        v => v && v.match(/[0-9A-Fa-f]{16}/g) !== null || 'Dev EUI must be valid'
      ],
      gatewayRules: [
        v => v && v.match(/^[aA-fF0-9]{16}$/g) !== null || 'Gateway ID must be valid'
      ]
    }
  },
  components: {
    'v-device-card-title': DeviceSettingsCardTitle
  },
  mounted () {
    EventBus.$emit('set-title', 'Device LoRa settings')
    if (this.configuration) {
      const lora = this.configuration.lora || {}
      this.region = lora.region
      this.data_rate = lora.data_rate
      this.otaa = lora.otaa
      this.abp = lora.abp
      if (lora.otaa) {
        this.otaa_dev_eui = lora.otaa.dev_eui
        this.otaa_nwk_key = lora.otaa.nwk_key
      }
      if (lora.abp) {
        this.abp_dev_eui = lora.abp.dev_eui
        this.abp_nwk_key = lora.abp.nwk_key
      }
      if (lora.gateway) {
        this.server = lora.gateway.server
        this.port = lora.gateway.port
        this.gateway_id = lora.gateway.gateway_id
        this.nano = true
        this.abp = false
        this.otaa = false
      }
    } else {
      EventBus.$emit(Events.loadingEnd)
      // redirect to device settings home
      this.$router.push({
        name: 'DeviceSettings',
        params: { id: this.$route.params.id }
      })
    }
  },
  watch: {
    // usefull for toggle checkbox and panels
    nano: function (value) {
      if (value) {
        this.abp = false
        this.otaa = false
      }
    },
    abp: function (value) {
      if (value) {
        this.nano = false
      }
    },
    otaa: function (value) {
      if (value) {
        this.nano = false
      }
    }
  },
  methods: {
    save: function () {
      // finally post datas ///
      let lora = {
        region: this.region,
        data_rate: this.data_rate
      }
      if (this.otaa) {
        lora.otaa = {
          dev_eui: this.otaa_dev_eui, nwk_key: this.otaa_nwk_key
        }
      }
      if (this.abp) {
        lora.abp = {
          dev_eui: this.abp_dev_eui, nwk_key: this.abp_nwk_key
        }
      }
      if (this.nano) {
        delete lora.data_rate
        lora.gateway = {
          server: this.server,
          port: this.port,
          gateway_id: this.gateway_id
        }
      }
      let url = Persistent.getters.baseUrl + '/api/v2/account/device/' + this.$route.params.id + '/configuration'
      this.$http.post(url, { lora }).then(function (res) {
        if (res.status === 200) {
          // redirect to device settings home
          this.$router.push({
            name: 'DeviceSettings',
            params: { id: this.$route.params.id }
          })
        } else {
          this.notifyError(res.body.status, res.body.error)
        }
      }, response => {
        console.log('error updating lora config:', response)
        this.notifyError(response.status, response.body.message)
      })
    },
    notifyError: function (code, message) {
      console.log(Events.apiError, code, message)
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    },
    navigate: function (component) {
      this.$router.push({
        name: component,
        params: {
          id: this.device._id
        }
      })
    }
  }
}
</script>
