<template>

<v-navigation-drawer
      temporary
      v-model="drawer"
      absolute
    >
      <v-list class="pa-1">
        <v-list-tile avatar tag="div">
          <v-icon>my_location</v-icon>
          <v-list-tile-content>
            <v-list-tile-title @click="navigate('/profile')">
              <p class="caption" style="cursor: pointer; margin-left: 18px;">
              {{ username }}
            </p>
            </v-list-tile-title>
          </v-list-tile-content>
          <v-list-tile-action>
            <v-btn icon @click.stop="toggle">
              <v-icon>chevron_left</v-icon>
            </v-btn>
          </v-list-tile-action>
        </v-list-tile>
      </v-list>
      <v-list class="pt-0" dense>
        <v-divider light></v-divider>
        <v-list-tile v-for="item in items" :key="item.title" @click="navigate(item.route)">
          <v-list-tile-action>
            <v-icon :class="item.classz" >{{ item.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title :color="item.classz">{{ item.title }} </v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <!--
        <v-list-tile href="https://www.youspot.me/documentation/"
          target="_blank"
          rel="noopener noreferrer">
          <v-list-tile-action>
            <v-icon >info</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title >Documentation</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      -->
      </v-list>
    </v-navigation-drawer>
</template>
<script>

import EventBus from '../event-bus.js'
import Events from '../store/event-api.js'
import router from '../router'
import Persistent from '../store/persistent'

export default {
  created: function () {
    EventBus.$on(Events.toggleDrawer, this.toggle)
  },
  methods: {
    toggle () {
      this.drawer = !this.drawer
    },
    navigate (location) {
      router.push(location)
    }
  },
  computed: {
    devicename () {
      try {
        return Persistent.getters.device.name
      } catch (e) {
        return ''
      }
    },
    username () {
      try {
        return Persistent.getters.user.fullName
      } catch (e) {
        return ''
      }
    },
    items () {
      if (this.$store.getters.user.advanced) {
        return this.itemsAdvanced
      } else {
        return this.itemsNonAdvanced
      }
    }
  },
  data () {
    return {
      drawer: null,
      itemsAdvanced: [
        { title: 'Devices', icon: 'nfc', route: '/devices', classz: 'device' },
        { title: 'Claim device', icon: 'card_giftcard', route: '/devices/claim', classz: 'accent--text' },
        { title: 'Provision device', icon: 'bluetooth', route: '/devices/gettingstarted', classz: 'add' },
        { title: 'Flash device', icon: 'usb', route: '/serial/flash', classz: 'add' },
        { title: 'Applications', icon: 'apps', route: '/applications', classz: 'applications' },
        { title: 'Projects', icon: 'public', route: '/projects', classz: 'project' },
        { title: 'Logout', icon: 'exit_to_app', route: '/logout', classz: 'project' }
      ],
      itemsNonAdvanced: [
        { title: 'Devices', icon: 'nfc', route: '/devices', classz: 'device' },
        { title: 'Claim device', icon: 'card_giftcard', route: '/devices/claim', classz: 'accent--text' },
        { title: 'Provision device', icon: 'bluetooth', route: '/devices/provision', classz: 'add' },
        { title: 'Logout', icon: 'exit_to_app', route: '/logout', classz: 'project' }
      ],
      mini: false,
      right: null
    }
  }
}
</script>
<style>
.v-list__tile__title, .font-weight-bold {
  font-size: 1.3em;
}
aside.v-navigation-drawer.v-navigation-drawer--absolute.v-navigation-drawer--open.v-navigation-drawer--temporary {
  z-index: 1002;
}
</style>
