<template>
<tr>
  <td>
    <v-select v-on:change="authSelected" label="Please select"
      :items="auth_types" v-model="wifi.auth_type"></v-select>
  </td>
  <td>
    <v-text-field v-on:change="wifiChanged" v-model="wifi.bssid"></v-text-field>
  </td>

  <td>
    <v-text-field label="username" v-model="wifi.username"
      v-on:change="wifiChanged" v-if="wifi.auth_type==='WPA2-EAP-PEAP'"></v-text-field>
  </td>

  <td>
    <v-text-field v-on:change="wifiChanged" label="password"
      v-if="wifi.auth_type!=='WPA2-EAP-TLS'" v-model="wifi.password"
      autocomplete="off" type="password"></v-text-field>
  </td>

  <td>
    <v-text-field label="identity" v-model="wifi.identity"
      v-on:change="wifiChanged" v-if="wifi.auth_type!=='WPA2'"></v-text-field>
  </td>

  <td>
    <v-text-field label="client key" v-model="wifi.client_key"
      v-on:change="wifiChanged" v-if="wifi.auth_type==='WPA2-EAP-TLS'"></v-text-field>
  </td>
  <td>
    <v-text-field label="client certificate"  v-model="wifi.client_cert"
      v-on:change="wifiChanged" v-if="wifi.auth_type==='WPA2-EAP-TLS'"></v-text-field>
  </td>

  <td>
    <v-text-field v-if="wifi.auth_type!=='WPA2'" v-on:change="wifiChanged"
      label="certificate authorities" v-model="wifi.cert_auths"></v-text-field>
  </td>

  <td width="80" class="text-xs-right">
    <v-btn large icon color="warning" @click.stop="deleteWifi()">
       <v-icon>delete</v-icon>
     </v-btn>
  </td>
  <v-confirm ref="confirm"></v-confirm>
</tr>
</template>

<script>
import Events from '../../../store/event-api.js'
import EventBus from '../../../event-bus.js'
import DialogConfirm from '@/components/global/DialogConfirm'

export default {
  name: 'WifiSettingsLine',
  props: ['data', 'index'],
  data: function () {
    return {
      wifi: {},
      external_antenna: false,
      auth_types: ['WPA2', 'WPA2-EAP-TLS', 'WPA2-EAP-PEAP']
    }
  },
  components: {
    'v-confirm': DialogConfirm
  },
  mounted () {
    this.wifi = Object.assign({}, this.data)
  },
  methods: {
    deleteWifi: async function () {
      if (await this.$refs.confirm.open('Delete wifi network', 'Are you sure?', {
        color: 'warning'
      })) {
        this.$emit('delete', this.index, this.wifi)
      }
    },
    wifiChanged: function () {
      if (this.wifi && this.wifi.bssid && this.wifi.bssid.length > 0) { this.$emit('change', this.index, Object.assign({}, this.wifi)) }
    },
    authSelected: function (type) {
      if (type === 'WPA2') {
        delete this.wifi['cert_auths']
        delete this.wifi['client_key']
        delete this.wifi['client_cert']
      }
      if (type === 'WPA2-EAP-TLS') {
        if (!this.wifi['cert_auths']) {
          this.wifi['cert_auths'] = '/flash/cert/wifi_cert_auths.pem'
        }
        if (!this.wifi['client_key']) {
          this.wifi['client_key'] = '/flash/cert/wifi_client_key.pem'
        }
        if (!this.wifi['client_cert']) {
          this.wifi['client_cert'] = '/flash/cert/wifi_client_cert.pem'
        }
      }
      if (type === 'WPA2-PEAP') {
        if (!this.wifi['cert_auths']) {
          this.wifi['cert_auths'] = '/flash/cert/wifi_cert_auths.pem'
        }
      }
      this.wifiChanged()
    },
    notifyError: function (code, message) {
      console.log(Events.apiError, code, message)
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    }
  }
}
</script>
