
<template>
  <v-container fill-height justify-center justify-start fluid grid-list-sm>
    <v-layout align-start wrap>
      <v-flex xs12>
        <v-card class="white">
          <v-card-text>
            <l-map
              @click="addMarker"
              style="width: 100%; height: 500px;"
              :zoom="zoom"
              :center="center"
              :marker="marker"
            >
              <l-tile-layer :url="url" :attribution="attribution"></l-tile-layer>
              <l-marker :lat-lng="marker"></l-marker>
            </l-map>
          </v-card-text>
        </v-card>
      </v-flex>
      <v-flex xs12>
        <v-card class="upy-card">
          <v-card-text>
            <v-form ref="form" v-model="valid">
              <v-layout row wrap pa-4>
                <v-flex md4 xs12>
                  <v-text-field
                    hint="country"
                    v-model="device_location_country"
                    label="Country"
                  ></v-text-field>
                </v-flex>
                <v-flex md4 xs12>
                  <v-text-field
                    hint="state"
                    v-model="device_location_state"
                    label="State"
                  ></v-text-field>
                </v-flex>
                <v-flex md4 xs12>
                  <v-text-field
                    hint="locality"
                    v-model="device_location_locality"
                    label="locality"
                  ></v-text-field>
                </v-flex>
              </v-layout>
            </v-form>
          </v-card-text>
          <v-layout absolute bottom right>
            <v-btn fab color="default" @click.native="navigate('DeviceSettings')">
              <v-icon>close</v-icon>
            </v-btn>
            <v-btn fab color="primary" @click.native="saveDevice()">
              <v-icon>done</v-icon>
            </v-btn>
          </v-layout>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { LMap, LTileLayer, LMarker } from 'vue2-leaflet'
import Events from '../../store/event-api.js'
import EventBus from '../../event-bus.js'
import Persistent from '../../store/persistent'

export default {
  name: 'OSMap',
  data () {
    return {
      device: {},
      output: null,
      zoom: 15,
      center: [48.413220, -1.219482],
      url: 'http://{s}.tile.osm.org/{z}/{x}/{y}.png',
      attribution: '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',
      marker: [48.413220, -1.219482],
      device_location_country: null,
      device_location_state: null,
      device_location_locality: null,
      valid: false,
      location_loaded: false
    }
  },
  mounted () {
    EventBus.$emit('set-title', 'Map device settings')
  },
  created () {
    this.loadDevice(this.$route.params.id)
  },
  components: {
    LMap,
    LTileLayer,
    LMarker
  },
  methods: {
    loadDevice: function () {
      let url = Persistent.getters.baseUrl + '/api/v2/account/device/' + this.$route.params.id + '/location'
      this.$http.get(url).then(function (res) {
        if (res.data.device) {
          this.device = res.data.device
          EventBus.$emit('set-title', this.device.name + ' geolocation settings')
          if (res.data && res.data.location) {
            this.location_loaded = true
            this.output = res.data.location
            this.device_location_country = res.data.location.country
            this.device_location_state = res.data.location.state
            this.device_location_locality = res.data.location.locality
            this.marker = res.data.location.marker
            this.center = res.data.location.marker
          } else {
            // this is a 500 error, server shall have served an clean new config
            this.location = {}
            this.notifyError(500, 'an empty location has been created')
            this.addDefaultMarker()
          }
        } else {
          this.addDefaultMarker()
        }
      }, response => {
        this.notifyError(response.status, response.statusText)
        this.addDefaultMarker()
      })
    },
    addDefaultMarker () {
      console.log('marker')
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(position => {
          let me = this
          setTimeout(function () {
            if (!me.location_loaded) {
              me.center = [position.coords.latitude, position.coords.longitude]
              me.marker = [position.coords.latitude, position.coords.longitude]
              me.updateLocationDetails(position.coords.latitude, position.coords.longitude)
            }
          }, 50)
        })
      }
    },
    addMarker (event) {
      this.marker = [event.latlng.lat, event.latlng.lng]
      this.updateLocationDetails(event.latlng.lat, event.latlng.lng)
    },
    updateLocationDetails (lat, lng) {
      let url = 'https://nominatim.openstreetmap.org/reverse?format=json&lat=' + lat + '&lon=' + lng + '&zoom=18&addressdetails=1'
      this.$http.get(url).then(function (res) {
        this.device_location_country = res.data.address.country
        this.device_location_state = res.data.address.state
        this.device_location_locality = res.data.address.city || res.data.address.road || res.data.address.town
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },
    saveDevice: function () {
      let url = Persistent.getters.baseUrl + '/api/v2/account/device/' + this.$route.params.id + '/location'
      let data = Object.assign({}, {
        country: this.device_location_country,
        state: this.device_location_state,
        locality: this.device_location_locality,
        marker: this.marker
      })
      this.$http.post(url, data).then(function (res) {
        this.$router.push({
          name: 'DeviceSettings',
          params: { id: this.$route.params.id }
        })
      }, response => {
        console.log(response)
        console.log('error here')
        this.notifyError(response.status, response.body.message)
      })
    },
    notifyError: function (code, message) {
      console.log(Events.apiError, code, message)
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    },
    navigate: function (component) {
      this.$router.push({
        name: component,
        params: {
          id: this.device._id
        }
      })
    }
  }
}
</script>

