<template>
  <v-layout>
    <v-flex class="text-xs-center xs12">

      <v-btn style="width: calc(100% - 16px);"
        class="primary mr-4"
        @click.stop="flash"
        :disabled="!canRun"
        >Flash</v-btn>

   </v-flex>
  </v-layout>
</template>

<script>
import config from '../../config'

export default {
  name: 'ApplyFlash',
  data: function () {
    return {
      pending: false
    }
  },
  computed: {
    canRun () {
      return !this.pending && this.$store.state.localFW
    },
    getLocalFW () {
      return this.$store.state.localFW
    }
  },
  methods: {
    flash: function () {
      this.pending = true
      let addr = '0x1000'
      let filename = '/tmp/application.bin'

      let url = config.API_LOCATION + 'write_flash?port=' + this.$store.state.serialPort +
      '&addr=' + addr + '&filename=' + filename
      this.$http.get(url).then((response) => {
        this.pending = false
        this.$store.commit('localFile', response.data.filename)
      }).catch(e => {
        this.pending = false
      })
    }
  }
}
</script>

