<template>
  <v-container row fluid fill-height>
    <v-layout row fluid  class="fill-height">
        <v-flex>
        <v-card class="upy-card">
          <v-card-title>
             User account settings
          </v-card-title>
          <v-card-text class="grow">
            <v-subheader>
              <span class="grey--text">
                {{ this.userDetails.username }}
              </span>
            </v-subheader>
            <v-form ref="form" v-model="valid">
              <v-layout row wrap pa-4>
                <v-flex md4 xs12>
                  <v-text-field
                    hint="full name"
                    v-model="fullName"
                    label="Full Name"
                  ></v-text-field>
                </v-flex>
                  </v-layout>
                <v-layout row wrap pa-4>
                <v-flex xs6>
                  <v-switch v-model="isDarkTheme" @change="switchTheme" :label="`${isDarkTheme? 'dark': 'light'} theme`"></v-switch>
                </v-flex>
                <v-flex xs6>
                  <v-switch v-model="isAdvanced" @change="switchAdvanced" label="advanced mode"></v-switch>
                </v-flex>
              </v-layout>
            </v-form>

          </v-card-text>
        </v-card>

        <v-expansion-panel class="mt-4" v-if="$store.getters.user.advanced">
          <v-expansion-panel-content>
            <template v-slot:header>
              <div>API Key</div>
            </template>
            <v-card>
              <v-card-text>
                <v-data-table
                  no-data-text="Please click + to create an API key."
                  :items="apiKeys"
                  class="elevation-1"
                  hide-actions
                  hide-headers
                >
                  <template v-slot:items="props">
                    <td>{{ props.item.description }}</td>
                    <td>{{ props.item.api_key }}</td>
                    <td>{{ props.item.created_at | formatDate }}</td>
                    <td v-if="props.item.last_used">{{ props.item.last_used  | formatDate }}</td>
                    <td v-if="!props.item.last_used">Not Used</td>
                    <td class="text-xs-right">
                      <v-icon @click="copy(props.item.api_key, $event)">file_copy</v-icon>
                      <v-icon v-if="!props.item.isDisabled" @click="disableApiKEy(props.item)">block</v-icon>
                      <v-icon v-if="props.item.isDisabled" @click="enableApiKEy(props.item)">remove_circle</v-icon>
                      <v-icon v-if="props.item.isDisabled" @click="deleteApiKEy(props.item)">clear</v-icon>
                    </td>
                  </template>
                </v-data-table>
              </v-card-text>
              <v-card-actions>
                <v-btn class="primary" @click="openDialog('APIKey')">
                  <v-icon>add</v-icon>
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-expansion-panel-content>
        </v-expansion-panel>


      </v-flex>
      <v-btn  fixed right bottom fab @click.native="saveProfile()" color="info">
          <v-icon>done</v-icon>
      </v-btn>



    </v-layout>





    <v-confirm ref="confirm"></v-confirm>
    <v-dialog-name ref="dialogName"></v-dialog-name>
    <v-api-key v-on:update="getAPIKeys()" :dialogSignal="apiKeyAdd"/>


  </v-container>
</template>

<script>
import Events from '../../store/event-api.js'
import EventBus from '../../event-bus.js'
import Persistent from '../../store/persistent'
import router from '../../router'
import DialogConfirm from '@/components/global/DialogConfirm'
import DialogName from '@/components/global/DialogName'
import APIKey from '@/components/UserProfile/APIKey'

export default {
  data () {
    return {
      valid: false,
      fullName: null,
      userDetails: {},
      copied: false,
      apiKeyAdd: false,
      apiKeys: [],
      dialog: false
    }
  },
  computed: {
    isDarkTheme: {
      get: () => {
        return Persistent.getters.isDarkTheme
      }
    },
    isAdvanced: {
      get: () => {
        return Persistent.getters.isAdvanced
      },
      set: () => {

      }
    }
  },
  components: {
    'v-confirm': DialogConfirm,
    'v-dialog-name': DialogName,
    'v-api-key': APIKey
  },
  created () {
    this.getAPIKeys()
  },
  mounted () {
    EventBus.$emit('set-title', 'User Profile')
    this.userDetails = Persistent.getters.user
    this.fullName = Persistent.getters.user.fullName
  },
  methods: {
    switchTheme () {
      Persistent.dispatch('changeTheme')
    },
    switchAdvanced () {
      Persistent.dispatch('changeAdvanced')
    },
    getComponent (componentName) {
      let component = null
      let parent = this.$parent
      while (parent && !component) {
        if (parent.$options.name === componentName) {
          component = parent
        }
        parent = parent.$parent
      }
      return component
    },
    getAPIKeys () {
      let url = Persistent.getters.baseUrl + '/api/v2/api-keys'
      this.$http.get(url).then(function (res) {
        this.apiKeys = res.body.apiKeys
      }, response => {
        console.log(response)
        console.log('error here')
        this.notifyError(response.status, response.body.message)
      })
    },
    saveProfile () {
      let url = Persistent.getters.baseUrl + '/api/v2/profile/userinfo'
      let data = {
        fullName: this.fullName,
        darkTheme: this.isDarkTheme
      }
      this.$http.post(url, data).then(function (res) {
        this.navigate({name: 'Devices'})
      }, response => {
        console.log(response)
        console.log('error here')
        this.notifyError(response.status, response.body.message)
      })
    },
    copy: function (value, event) {
      const el = document.createElement('textarea')
      el.value = value
      el.setAttribute('readonly', '') // Make it readonly to be tamper-proof
      el.style.position = 'absolute'
      el.style.left = '-9999px'
      document.body.appendChild(el)
      const selected =
        document.getSelection().rangeCount > 0 // Check if there is any content selected previously
          ? document.getSelection().getRangeAt(0) // Store selection if found
          : false
      el.select()
      document.execCommand('copy')
      document.body.removeChild(el)
      if (selected) {
        document.getSelection().removeAllRanges() // Unselect everything on the HTML document
        document.getSelection().addRange(selected)
      }
      let me = event
      let oldText = event.target.innerText
      event.target.innerText = 'done'
      event.target.classList.add('primary')
      setTimeout(function () {
        me.target.innerText = oldText
        me.target.classList.remove('primary')
      }, 1000)
    },
    disableApiKEy: async function (item) {
      const confirm = await this.$refs.confirm.open('Disable API KEy', 'Are you sure you want to disable API KEY?', {
        color: 'info'
      })
      if (confirm) {
        let url = Persistent.getters.baseUrl + '/api/v2/api-keys/' + item._id
        let data = {
          isDisabled: true
        }
        this.$http.post(url, data).then(function (res) {
          item.isDisabled = true
        }, response => {
          console.log(response)
          console.log('error here')
          this.notifyError(response.status, response.body.message)
        })
      }
    },
    deleteApiKEy: async function (item) {
      const confirm = await this.$refs.confirm.open('Delete API KEy', 'Are you sure you want to delete API KEY?', {
        color: 'warning'
      })
      if (confirm) {
        let url = Persistent.getters.baseUrl + '/api/v2/api-keys/' + item._id
        this.$http.delete(url).then(function (res) {
          let targetIndex = null
          this.apiKeys.forEach((element, index) => {
            if (element._id === item._id) {
              targetIndex = index
            }
          })
          if (targetIndex != null) {
            this.apiKeys.splice(targetIndex, 1)
          }
        }, response => {
          console.log(response)
          console.log('error here')
          this.notifyError(response.status, response.body.message)
        })
      }
    },
    enableApiKEy: async function (item) {
      const confirm = await this.$refs.confirm.open('Enable API KEy', 'Are you sure you want to enable API KEY?', {
        color: 'info'
      })
      if (confirm) {
        let url = Persistent.getters.baseUrl + '/api/v2/api-keys/' + item._id
        let data = {
          isDisabled: false
        }
        this.$http.post(url, data).then(function (res) {
          item.isDisabled = false
        }, response => {
          console.log(response)
          console.log('error here')
          this.notifyError(response.status, response.body.message)
        })
      }
    },
    notifyError: function (code, message) {
      console.log(Events.apiError, code, message)
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    },
    navigate (location) {
      router.push(location)
    },
    openDialog: function (component) {
      if (component === 'APIKey') {
        this.apiKeyAdd = !this.apiKeyAdd
      }
    }
  }
}
</script>

