<template>
      <v-dialog v-model="show" width="320">
        <v-card class="upy-card">
          <v-card-title>
            <h2>Set a pincode</h2>
          </v-card-title>
          <v-card-text>
            <p>Protect access to this device</p>
            <v-layout wrap>
            <v-flex xs9>
                <v-text-field type="password" :rules="ruleReq" v-model="pinCode" label="Pincode" required></v-text-field>
            </v-flex>
            <v-flex xs9>
                <v-text-field type="password" :rules="ruleMatch" v-model="pinCode2" label="Pincode (again)" required></v-text-field>
            </v-flex>
          </v-layout>
          </v-card-text>
        <v-card-actions>
          <v-btn disabled:="!valid" color="primary" raised @click.stop="toggle()">Done</v-btn>
          </v-card-actions>
        </v-card>
   </v-dialog>
</template>

<script>
// import EventBus from '../../event-bus.js'
export default {
  name: 'BLESetPincode',
  props: ['show'],
  data () {
    return {
      pinCode: '',
      pinCode2: '',
      ruleMatch: [
        (v) => v === this.pinCode || 'Pincodes must match'
      ],
      ruleReq: [
        (v) => !!v || 'Pincode is required'
      ]
    }
  },
  computed: {
    valid: function () {
      return this.pinCode.length > 0
    }
  },
  methods: {
    toggle () {
      if (this.valid) {
        this.$emit('registerPin', this.pinCode)
      }
    }
  }
}
</script>
