<template>
  <v-container fluid grid-list-md ma-0 pa-0 v-if="device">
    <v-layout align-content-center row wrap >
      <v-toolbar>
        <v-btn v-if="device.project" class="info"
          @click.stop="navigate('Devices')">
          <v-icon left>nfc</v-icon>
          all devices
        </v-btn>
        <v-spacer></v-spacer>
        <v-btn v-if="device.project" class="info"
          @click.stop="navigate('ProjectSettings', device.project._id)">
          <v-icon left>settings</v-icon>
            {{device.project.name}}
        </v-btn>
        <v-btn class="info"
          @click.stop="navigate('DeviceSettings', device._id)">
          <v-icon left>nfc</v-icon>
          {{device.name}}
        </v-btn>
        <v-device-stream :stream='stream' @onState="onState" @onData="onData" :device='device'></v-device-stream>
      </v-toolbar>
  <v-flex xs12 class="pa-4">
    <v-tabs
      class="mt-0 pt-0 elevation-2"
      v-model="mode"
      centered>
        <v-tab key="datatable" ripple>Data table</v-tab>
        <v-tab key="charts" ripple>Charts</v-tab>
        <v-tab key="gauge" ripple>Gauge</v-tab>
        <v-tab key="export" disabled ripple>Export</v-tab>
        <v-tab-item key="datatable" >
          <v-data-table
            v-bind:pagination.sync="pagination"
              :headers="headers"
              :items="messages"
               item-key="ts"
              class="mb-5"
            >
            <template slot="items" slot-scope="props">
              <tr @click="props.expanded = !props.expanded ">
              <td @click="itemClick()">{{new Date(props.item.ts).toLocaleString()}}</td>
              <td @click="itemClick()" class="text-truncate text-no-wrap">{{props.item.topic}}</td>
              <td @click="itemClick()" class="text-truncate text-no-wrap myellipsis">
                <div class="text-truncate text-no-wrap datacol">
                {{props.item.data}}
              </div>
              </td>
            </tr>
            </template>
            <template slot="expand" slot-scope="props">
              <v-card  flat :key="props.item.ts">
                <v-card-text>
                  <v-container fluid grid-list-md>
                    <v-layout align-content-center wrap>
                      <v-flex xs12 md6 offset-md4>
                        <pre class="black white--text"
                          style="background-color: black;">{{props.item}}</pre>
                      </v-flex>
                    </v-layout>
                  </v-container>
                </v-card-text>
              </v-card>
          </template>
        </v-data-table>
      </v-tab-item>
      <!-- device charts -->
      <v-tab-item key="charts">
        <v-device-charts
          v-if="messages.length > 0"
          :active="mode === 1"
          :messages="messages"
          @onChange='onChartChange'>
        </v-device-charts>
      </v-tab-item>
      <!-- device charts -->
      <v-tab-item key="gauges">
        <v-device-gauges :active="mode === 2" :message="message"></v-device-gauges>
      </v-tab-item>
    </v-tabs>
  </v-flex>
</v-layout>
</v-container>
</template>

<script>
import JSONEditor from 'vue2-jsoneditor'
import Events from '../../store/event-api.js'
import Persistent from '../../store/persistent'
import EventBus from '../../event-bus.js'
import DeviceTitle from '../global/DeviceTitle.vue'
import DeviceStream from './DeviceStream'
import DeviceCharts from './DeviceCharts'
import DeviceGauges from './DeviceGauges'

export default {
  name: 'DeviceData',
  data () {
    return {
      MAX_STACK: 100,
      client: null,
      stream: false,
      user: Persistent.getters.user,
      device: null,
      messages: [],
      message: {},
      mode: 'datatable',
      pagination: {'sortBy': 'ts', 'descending': true, 'rowsPerPage': 10},
      headers: [
        {
          text: 'timestamp',
          align: 'left',
          value: 'ts'
        },
        { text: 'Topic', sortable: false, value: 'topic', width: '1%' },
        { text: 'Data', sortable: false, value: 'data', width: '1%;' }
      ]
    }
  },
  components: {
    'json-editor': JSONEditor,
    'v-device-charts': DeviceCharts,
    'v-device-title': DeviceTitle,
    'v-device-stream': DeviceStream,
    'v-device-gauges': DeviceGauges
  },
  mounted () {
    EventBus.$emit('set-title', 'data')
  },
  created () {
    this.loadDevice(this.$route.params.id)
  },
  beforeDestroy () {
    if (this.client) {
      this.client.end()
    }
  },
  watch: {
    pagination: {
      handler (v) {
        this.stream = !this.stream
        if (v.page === 1) {
          this.stream = true
        } else {
          this.stream = false
        }
      },
      deep: true
    }
  },
  methods: {
    navigate: function (name, id) {
      this.$router.push({
        name,
        params: {
          id
        }
      })
    },
    onChartChange (p) {
      if (p.page !== 1) {
        this.stream = false
      } else {
        this.stream = true
      }
    },
    itemClick () {
      this.stream = false
      return true
    },
    init () {
      this.getBefore()
    },
    onState (s) {
      this.stream = s
    },
    onData (topic, message) {
      const now = parseInt(new Date().getTime() / 1000, 10)
      this.getAfter(now, 100)
    },
    fillTop (data) {
      const lastTime = this.messages[0] ? this.messages[0].ts : 0
      const messages = this.messages
      for (let i = 0; i < data.length; i++) {
        if (data[i].ts <= lastTime) {
          break
        }
        messages.unshift(Object.assign({}, {
          topic: data[i].topic,
          ts: data[i].ts,
          data: data[i].data
        }))
        if (messages.length > this.MAX_STACK) {
          messages.pop()
        }
      }
      this.messages = messages
      this.message = messages[0]
    },
    parseData (data) {
      let messages = []
      for (let i = 0; i < data.length; i++) {
        messages.push(Object.assign({}, {
          topic: data[i].topic, // .split('/')[3],
          ts: data[i].ts,
          data: data[i].data
        }))
      }
      this.messages = messages
      this.message = this.messages[0]
    },
    getAfter (start, limit = 100) {
      const id = this.device._id
      let url = Persistent.getters.baseUrl + '/api/v2/data/device/' + id + '/after/' + start + '/' + limit
      this.$http.get(url).then(function (res) {
        if (res.data.error) {
          this.notifyError(500, res.data.error.errmsg)
          this.client.end()
          return
        }
        this.fillTop(res.data.messages)
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },
    getBefore (start = parseInt(new Date().getTime() / 1000), limit = 100) {
      const id = this.device._id
      let url = Persistent.getters.baseUrl + '/api/v2/data/device/' + id + '/before/' + start + '/' + limit
      this.$http.get(url).then(function (res) {
        if (res.data.error) {
          this.notifyError(500, res.data.error.errmsg)
          this.client.end()
          return
        }
        this.parseData(res.data.messages)
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },
    loadDevice: function (id) {
      let url = Persistent.getters.baseUrl + '/api/v2/account/device/' + id
      this.$http.get(url).then(function (res) {
        if (res.data && res.data.device && res.data.device._id) {
          this.device = res.data.device
          this.client = this.init()
          EventBus.$emit('set-title', this.device.name + ' data stream')
          // this.update_on_boot = this.device.configuration.update_on_boot
        } else {
          this.notifyError(500, 'unexpected error DeviceSettings.loadDevice')
          if (this.client) {
            this.client.end()
          }
          this.$router.push({name: 'Devices'})
        }
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },
    notifyError: function (code, message) {
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    },
    notifySuccess: function (code, message) {
      EventBus.$emit(Events.apiSuccess, code, message)
    },
    back: function () {
      this.$router.push({
        name: 'Devices'
      })
    }
  }
}

</script>
