
<template>
  <v-dialog v-model="dialog" max-width="640" @keydown.esc="cancel">
    <v-card class="upy-card">
      <v-toolbar :color="options.color" dense flat>
        <v-toolbar-title class="primary--text">Bluetooth on-boarding</v-toolbar-title>
      </v-toolbar>
      <v-card-text>
        You can create a new device or use a template to prepare a device.
        <v-select label="select template" :disabled="this.templates.length === 0"></v-select>
        On the next step you will be redirected to device configuration screen.
        When you configuration is ready, use the Bluetooth <b>Ensure Config</b> tool for
        for saving settings inside the device
      </v-card-text>
      <v-card-actions class="pt-0">
        <v-btn @click.native="cancel">Cancel</v-btn>
        <v-btn primary @click.native="save" disabled>Copy from template</v-btn>
        <v-btn primary @click.native="save">Create new device</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>

export default {
  data: () => ({
    templates: [],
    dialog: false,
    resolve: null,
    reject: null,
    message: null,
    title: null,
    device: { _id: -1 },
    options: {
      color: 'primary',
      width: 290
    }
  }),
  methods: {
    open (title, message, options) {
      this.dialog = true
      this.title = title
      this.message = message
      this.name = ''
      this.options = Object.assign(this.options, options)
      return new Promise((resolve, reject) => {
        this.resolve = resolve
        this.reject = reject
      })
    },
    save () {
      this.resolve(this.device)
      this.dialog = false
    },
    cancel () {
      this.resolve(false)
      this.dialog = false
    }
  }
}
</script>
