<template>
  <v-container fluid grid-list-md>
    <v-layout  align-content-center wrap>
      <v-flex xs12  d-flex>
        <v-card class="upy-card" height="100%">
          <v-card-title class="applications">General</v-card-title>
            <v-card-text>
              <v-layout>
              <v-flex xs8>
                    <v-text-field
                      full-width
                      label="application name"
                      v-model="application.name">
                    </v-text-field>
                  </v-flex>
                  <v-flex xs4>
                      <v-text-field
                      full-width
                      placeholder="..."
                      label="version code"
                      v-model="application.version">
                    </v-text-field>
                    </v-flex>
                  </v-layout>
            </v-card-text>
          </v-card>
      </v-flex>
<!--
        <v-flex  pa-1 d-flex>
          <v-card class="upy-card">
            <v-card-title class="applications">Advanced</v-card-title>
            <v-card-text>
              <v-layout row wrap>

                <v-flex xs12 md6>
                  <v-switch
                    disabled
                    v-model="config.enable_rpc"
                    label="Enable RPC">
                  </v-switch>
                </v-flex>
              <v-flex xs12 md6>
                <v-switch
                  disabled
                  v-model="application.with_lora"
                  label="Enable on LoRa Server">
                </v-switch>
            </v-flex>
            </v-layout>
        </v-card-text>
        </v-card>
      </v-flex>
    -->

    <v-flex d-flex xs12 md12 pa-1 v-if="code_origin == 'fw_build'">
      <v-fw-build-upload
        @update="loadApplication(application._id)"
        :application="application">
      </v-fw-build-upload>
    </v-flex>

      <v-flex d-flex xs12 pa-1>
        <v-flash-upload
          @update="loadApplication(application._id)"
          :application="application">
        </v-flash-upload>
      </v-flex>




      <v-flex d-flex xs12>
        <v-card class="upy-card flexcard" height="100%">
          <v-card-title class="applications">Advanced</v-card-title>
          <v-card-text class="grow">
            <v-list two-line>

                <v-divider class="mr-3"></v-divider>
                <v-list-tile @click="navigate('ApplicationSettingsAdvanced')">
                    <v-list-tile-content>
                        <v-list-tile-title >
                          Application configuration
                        </v-list-tile-title>
                        <v-list-tile-sub-title>
                          Parameters available on all devices
                        </v-list-tile-sub-title>
                    </v-list-tile-content>
                    <v-list-tile-action>
                      <v-icon>settings</v-icon>
                    </v-list-tile-action>
                </v-list-tile>

                <v-divider class="mr-3"></v-divider>
                <v-list-tile @click="navigate('ApplicationCodec')">
                    <v-list-tile-content>
                        <v-list-tile-title >
                          Codec
                        </v-list-tile-title>
                        <v-list-tile-sub-title>
                          Decode payload to data
                        </v-list-tile-sub-title>
                    </v-list-tile-content>
                    <v-list-tile-action>
                      <v-icon>code</v-icon>
                    </v-list-tile-action>
                </v-list-tile>

              <v-divider class="mr-3"></v-divider>
            </v-list>
          </v-card-text>
          <v-card-actions>
            <v-btn @click="deleteApplication()" class="warning">
              Delete application
            </v-btn>
            <!-- <v-btn flat class="info">Download</v-btn> -->
          </v-card-actions>
        </v-card>
      </v-flex>



    <v-btn fab fixed bottom right color="info" @click.native="saveApplication()">
        <v-icon>done</v-icon>
    </v-btn>

    <v-confirm ref="confirm"></v-confirm>
  </v-layout>
 </v-container>
</template>

<script>

import EventBus from '../../event-bus.js'
import Events from '../../store/event-api.js'
import Persistent from '../../store/persistent'
import DialogConfirm from '@/components/global/DialogConfirm'
import MQTTSettings from '../global/MQTTSettings'
import FWBuildUpload from './FWBuildUpload'
import FlashUpload from './FlashUpload'
// import CertUpload from '../global/CertificateUpload

export default {
  name: 'Application',
  data () {
    return {
      user: Persistent.getters.user,
      platform_default: true,
      application: {},
      code_origin: 'fw_build',
      config: { }
    }
  },
  components: {
    'v-confirm': DialogConfirm,
    'v-fw-build-upload': FWBuildUpload,
    'v-flash-upload': FlashUpload,
    'v-mqtt': MQTTSettings
  },
  mounted () {
    this.loadApplication(this.$route.params.id)
  },
  methods: {

    navigate: function (component) {
      this.$router.push({
        name: component,
        params: {
          id: this.application._id,
          configuration: this.configuration,
          application: this.application
        }
      })
    },
    mqttEdit: function (mqtt) {
      this.application.mqtt = mqtt
    },
    saveApplication: function () {
      let url = Persistent.getters.baseUrl + '/api/v2/application/' + this.application._id
      let data = this.application
      this.$http.post(url, data).then(function (res) {
        if (res.body.status === 200) {
          this.$router.push({
            name: 'Applications'
          })
        } else {
          this.notifyError(res.body.status, res.body.error)
        }
      }, response => {
        console.error(response)
        this.notifyError(response.status, response.body.message)
      })
    },
    loadApplication: function (id) {
      let url = Persistent.getters.baseUrl + '/api/v2/application/' + id
      this.$http.get(url).then(function (res) {
        if (res.data.status && res.data.status === 200) {
          if (res.data && res.data.application) {
            this.application = res.data.application
            console.log(this.application)
          }
        } else {
          this.notifyError(res.data.status, res.data.error)
        }

        EventBus.$emit('set-title', `edit app ${res.data.application.slug}`)
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },
    deleteApplication: async function (id) {
      if (await this.$refs.confirm.open('Delete application', 'Are you sure?', {
        color: 'warning'
      })) {
        let url = Persistent.getters.baseUrl + '/api/v2/application/' + this.application._id
        this.$http.delete(url).then(function (res) {
          this.$router.push({
            name: 'Applications'
          })
        }, response => {
          this.notifyError(response.status, response.statusText)
        })
      }
    },
    notifyError: function (code, message) {
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    }
  }
}
</script>
<style scoped>

.v-card-text-app-setting {
   min-height:230px;
 }
</style>
