
<template>
  <v-layout>
      <v-flex mt-2 xs12 offset-md2 md8>

          <v-webhook @onChange='onWebhookChanged' :webhook='this.webhook' v-if='this.webhook'/>

          <v-card class="upy-card" mt-2 v-if='!this.webhook'>
            <v-card-text pb-4 class="pb-5">

            <v-list subheader two-line >
                <v-subheader>Webhooks</v-subheader>
                <template  v-for="(webhook, index) in webhooks">
                  <span :key="index">
                  <v-divider  class="mr-3"></v-divider>
                  <v-list-tile  @click="editWebhook(index)" class="mr-3">
                      <v-list-tile-content>
                          <v-list-tile-title>
                            {{webhook.method}} {{webhook.url}}
                           </v-list-tile-title>
                      </v-list-tile-content>
                      <v-list-tile-action>
                        <v-icon>edit</v-icon>
                      </v-list-tile-action>
                  </v-list-tile>
                </span>
              </template>
            </v-list>
          </v-card-text>
          <v-card-actions>
            <v-btn flat class="primary"  @click="addWebhook()">Add webhook</v-btn>
          </v-card-actions>
          <v-btn  absolute right bottom fab @click="saveWebhooks()" color="info">
              <v-icon>done</v-icon>
          </v-btn>
          </v-card>

      </v-flex>

  </v-layout>
</template>

<script>

import EventBus from '../../event-bus.js'
import Events from '../../store/event-api.js'
import Persistent from '../../store/persistent'
import Webhook from './Webhook'

const EMPTY_WEBHOOK = {
  method: 'POST',
  filters: [],
  url: null,
  headers: [{ key: 'Content-Type', value: 'application/json' }],
  body: '{ data: {{data}}, payload: {{payload}}, topic: {{topic}} }'
}

export default {
  name: 'Webhooks',
  components: {
    'v-webhook': Webhook
  },
  data () {
    return {
      project: {},
      user: Persistent.getters.user,
      webhooks: [],
      webhook: null,
      editedIndex: -1
    }
  },
  mounted () {
    this.loadProject(this.$route.params.id)
    EventBus.$emit('set-title', 'webhooks')
  },
  methods: {
    onWebhookChanged: function (wh) {
      console.log('wh changed', wh)
      if (wh === null) {
        // remove
        this.webhooks.splice(this.editItem, 1)
      } else {
        // update
        this.webhooks[this.editItem] = wh
      }
      // close the view
      this.webhook = null
      this.editItem = -1
      this.saveWebhooks(false)
    },
    editWebhook: function (id) {
      this.editItem = id
      this.webhook = Object.assign({}, this.webhooks[id])
    },
    addWebhook: function () {
      this.webhooks.push(Object.assign({}, EMPTY_WEBHOOK))
      this.editWebhook(this.webhooks.length - 1)
    },
    notifyError: function (code, message) {
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    },
    loadProject: function (id) {
      let url = Persistent.getters.baseUrl + '/api/v2/project/' + id
      this.$http.get(url).then(function (res) {
        if (res.data && res.data.project) {
          this.project = res.data.project
          this.webhooks = res.data.project.webhooks || []
          EventBus.$emit('set-title', this.project.name + '\'s webhooks')
        }
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },
    saveWebhooks: function (redirect = true) {
      let url = Persistent.getters.baseUrl + '/api/v2/project/' + this.$route.params.id + '/webhooks'
      // prevent update if no changes
      // if (JSON.stringify(this.webhooks) === JSON.stringify(this.project.webhooks)) {
      //   if (redirect) {
      //     this.$router.push({
      //       name: 'ProjectSettings',
      //       params: { id: this.$route.params.id }
      //     })
      //   }
      //   return
      // }
      // post data
      this.$http.post(url, {webhooks: this.webhooks}).then(function (res) {
        if (res.body.status === 200) {
          if (redirect) {
            this.$router.push({
              name: 'ProjectSettings',
              params: { id: this.$route.params.id }
            })
          }
        } else {
          this.notifyError(res.body.status, res.body.error)
        }
      }, response => {
        console.log(response)
        this.notifyError(response.status, response.body.message)
      })
    }
  }
}
</script>
