<template>
  <span v-if="options">

    <v-data-table
    color="black"
    :rows-per-page-items="[ 5, 10, 15, 20, 25, 50, 75, 100]"
      :total-items="this.MAX_STACK"
        hide-headers
        :pagination.sync="pagination"
        :items="messages"
         item-key="ts"
        class="elevation-1"
      >
      <template slot="items" slot-scope="props">
      </template>
    </v-data-table>

    <v-line-chart
    class="pa-2"
      v-if="chartdata && options"
      :pagination='pagination'
      :chart-data="chartdata"
      :options="options">
    </v-line-chart>
    <!--       -->

  </span>
</template>

<script>
import LineChart from './LineChart'

export default {
  props: ['messages', 'active'],
  data: () => ({
    stream: false,
    pagination: { rowsPerPage: 100 },
    keys: [],
    datasets: [],
    loaded: true,
    chartdata: null,
    MAX_STACK: 100,
    options: null,
    colors: {}
  }),
  components: {
    'v-line-chart': LineChart
  },
  methods: {
    getColor (key) {
      if (!this.colors[key]) {
        this.colors[key] = this.dynamicColors()
      }
      return this.colors[key]
    },
    dynamicColors () {
      var r = Math.floor(Math.random() * 255)
      var g = Math.floor(Math.random() * 255)
      var b = Math.floor(Math.random() * 255)
      return 'rgb(' + r + ',' + g + ',' + b + ')'
    },
    buildDataLabels (msgs, clean = true) {
      let datasets = []
      if (!clean) {
        // datasets = this.datasets
      }
      const labels = []
      const options = Object.assign({}, {
        responsive: true,
        maintainAspectRatio: false
      })
      options.scales = {}
      options.scales.yAxes = []
      const start = ((this.pagination.page - 1) * this.pagination.rowsPerPage) || 0
      for (let i = start; i < start + (this.pagination.rowsPerPage || 20); i++) {
        if (!msgs[i] || !msgs[i].ts || !msgs[i].data) {
          continue
        }
        // add date on x-axys
        labels.unshift(new Date(msgs[i].ts).toLocaleString())
        // parse all data objects properties
        for (var prop in msgs[i].data) {
          const val = msgs[i].data[prop]
          if (!isNaN(val)) {
            // check for prop in labels
            let dataset = datasets.find(d => d.label === prop)
            if (!dataset) {
              // options
              const y = {
                type: 'linear',
                display: true,
                position: datasets.length % 2 === 0 ? 'left' : 'right',
                id: 'y-axis-' + datasets.length
              }
              options.scales.yAxes.unshift(y)
              this.options = Object.assign({}, options) // Object.assign ?
              const color = this.getColor(prop)
              // datasets
              dataset = {
                label: prop,
                borderColor: color,
                backgroundColor: color,
                fill: false,
                data: [val],
                yAxisID: 'y-axis-' + datasets.length
              }
              datasets.unshift(dataset)
            } else {
              dataset.data.unshift(val)
            }
          }
        }
      }
      return { datasets, labels }
    }
  },
  watch: {
    pagination: function (v) {
      this.chartdata = null
      const { datasets, labels } = this.buildDataLabels(this.messages)
      this.chartdata = {
        labels: labels.slice(),
        datasets: datasets.slice()
      }
      this.$emit('onChange', this.pagination)
    },
    active: function (val) {
      if (val) {
        var me = this
        setTimeout(function () {
          const { datasets, labels } = me.buildDataLabels(me.messages)
          me.chartdata = Object.assign({
            labels: labels,
            datasets: datasets
          }, me.chartdata)
        }, 100)
      }
    },
    messages: function (val) {
      if (!this.chartdata) return // dont know render well otherwise
      const { datasets, labels } = this.buildDataLabels(val, false)
      this.chartdata = {
        labels: labels,
        datasets: datasets
      }
    }
  }
}
</script>
