
<template>
  <v-layout>
      <v-flex mt-4 xs12 offset-md2 md8>
          <v-card class="upy-card ma-2">
            <v-card-text pb-4 class="pb-5">
            <v-list subheader two-line>
                <v-subheader class="project">Projects</v-subheader>
                <template  v-for="(project, index) in projects">
                  <v-divider :key="index" class="mr-3"></v-divider>
                  <v-list-tile  :key="project._id"  @click="openProject(project._id)" class="mr-3">
                      <v-list-tile-content>
                          <v-list-tile-title>{{project.name}}</v-list-tile-title>
                          <v-list-tile-sub-title></v-list-tile-sub-title>
                      </v-list-tile-content>
                      <v-list-tile-action>
                        <v-icon>settings</v-icon>
                      </v-list-tile-action>
                  </v-list-tile>
                </template>
            </v-list>
          </v-card-text>

          </v-card>
          <v-btn  fixed right bottom fab @click="addProject" color="info">
              <v-icon>add</v-icon>
          </v-btn>
      </v-flex>
      <v-dialog-name ref="confirm"></v-dialog-name>
  </v-layout>
</template>

<script>

import EventBus from '../../event-bus.js'
import Events from '../../store/event-api.js'
import Persistent from '../../store/persistent'
import DialogName from '@/components/global/DialogName'

export default {
  name: 'Projects',
  props: ['config'],
  data () {
    return {
      user: Persistent.getters.user,
      projects: []
    }
  },
  components: {
    'v-dialog-name': DialogName
  },
  created () {
    this.loadProjects()
  },
  mounted () {
    EventBus.$emit('set-title', 'manage projects')
  },
  methods: {
    openProject: function (id) {
      this.$router.push({
        name: 'ProjectSettings',
        params: {
          id: id
        }
      })
    },
    addProject: async function () {
      const name = await this.$refs.confirm.open('Add project', 'Please set an project name', {
        color: 'info'
      })
      if (!name) {
        this.notifyError(400, 'project name is required')
        return
      }
      let url = Persistent.getters.baseUrl + '/api/v2/projects'
      this.$http.put(url, {name}).then(function (res) {
        if (res.data.status === 200 && res.data.project.slug) {
          this.$router.push({name: 'ProjectSettings', params: { id: res.data.project._id }})
        } else {
          console.log(res.data)
          this.notifyError(res.body.status, res.body.error)
        }
      }, response => {
        console.log(response)
        this.notifyError(response.status, response.body.message)
      })
    },
    loadProjects: function () {
      let url = Persistent.getters.baseUrl + '/api/v2/projects'
      this.$http.get(url).then(function (res) {
        if (res.body.data !== null) {
          this.projects = res.body.projects
        }
      }, response => {
        console.error(response)
        this.notifyError(response.status, response.body.message)
      })
    },
    notifyError: function (code, message) {
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    }
  }

}

</script>
