
<template>
  <v-dialog v-model="dialog" :max-width="options.width" @keydown.esc="cancel">
    <v-card class="upy-card">
      <v-toolbar :color="options.color" dense flat>
        <v-toolbar-title class="primary--text">{{ title }}</v-toolbar-title>
      </v-toolbar>
      <v-card-text v-show="!!message">
        {{ message }}
        <v-text-field class="mr-4 ml-2" prepend-icon="edit" v-model="name" v-if="dialog" autofocus></v-text-field>
      </v-card-text>
      <v-card-actions class="pt-0">
        <v-spacer></v-spacer>
        <v-btn  @click.native="cancel">Cancel</v-btn>
        <v-btn color="info primary--text"  @click.native="save">Save</v-btn>

      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
/**
   * Vuetify Confirm Dialog component
   * courtesy of https://gist.github.com/eolant/ba0f8a5c9135d1a146e1db575276177d
   */
export default {
  data: () => ({
    dialog: false,
    resolve: null,
    reject: null,
    message: null,
    title: null,
    name: '',
    options: {
      color: 'primary',
      width: 290
    }
  }),
  methods: {
    open (title, message, options) {
      this.dialog = true
      this.title = title
      this.message = message
      this.name = ''
      this.options = Object.assign(this.options, options)
      return new Promise((resolve, reject) => {
        this.resolve = resolve
        this.reject = reject
      })
    },
    save () {
      this.resolve(this.name)
      this.dialog = false
    },
    cancel () {
      // this.resolve(false)
      this.dialog = false
    }
  }
}
</script>
