
<template>
  <v-card class="upy-card" mt-2>
    <v-card-text pb-4 class="pb-5">
      <v-layout row wrap>
      <v-flex mt-3 xs2>
        <v-btn :color='btnColor'
          @click='toggleMethod()'>
          {{ webhook.method }}
        </v-btn>
      </v-flex>
      <v-flex xs10 pr-1>
        <v-text-field
          hide-details
          outline
          full-width
          label="url"
          :rules="[rules.required, rules.url]"
          v-model="webhook.url">
        </v-text-field>
      </v-flex>

          <v-expansion-panel class="mt-4" popout v-model="panel">
            <v-expansion-panel-content key="1">
              <template v-slot:header>
                <div>Filter</div>
              </template>
              <v-message-filter :filters='webhook.filters' @onChange='onFiltersChange'/>
            </v-expansion-panel-content>

            <v-expansion-panel-content key="2">
              <template v-slot:header>
                <div>Headers</div>
              </template>
              <v-http-headers :headers='webhook.headers' @onChange='onHeadersChange'/>
            </v-expansion-panel-content>

            <v-expansion-panel-content key="3">
              <template v-slot:header>
                <div>Body</div>
              </template>
              <codemirror
              class='bodyCode'
                @change="bodyChange"
                ref='editor'
                :options='editorOptions'
                :value="webhook.body">
              </codemirror>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-layout>

  </v-card-text>
  <v-card-actions>
    <v-btn flat class="warning"  @click="deleteWebhook">delete</v-btn>
  </v-card-actions>

  <v-btn  absolute right bottom fab @click="submit" color="info">
        <v-icon>done</v-icon>
  </v-btn>

  </v-card>
</template>

<script>
// import EventBus from '../../event-bus.js'
// import Events from '../../store/event-api.js'
// import Persistent from '../../store/persistent'
import HTTPHeaders from './HTTPHeaders'
import MessageFilters from './MessageFilters'

require('codemirror/mode/javascript/javascript')
require('codemirror/mode/vue/vue')

require('codemirror/addon/hint/show-hint.js')
require('codemirror/addon/hint/show-hint.css')
require('codemirror/addon/hint/javascript-hint.js')

// const DEFAULT_HEADERS = [{ key: 'Content-Type', value: 'application/json'}]
const URL_PATTERN = /(http|https):\/\/(\w+:{0,1}\w*)?(\S+)(:[0-9]+)?(\/|\/([\w#!:.?+=&%!\-/]))?/
export default {
  props: ['webhook'],
  name: 'Webhooks',
  components: {
    'v-http-headers': HTTPHeaders,
    'v-message-filter': MessageFilters
  },
  data () {
    return {
      panel: null,
      editorOptions: {lineNumber: false},
      rules: {
        required: value => !!value || 'Required.',
        url: value => {
          return URL_PATTERN.test(value) || 'Invalid e-mail.'
        }
      }
    }
  },
  computed: {
    btnColor: function () {
      if (this.webhook.method === 'GET') {
        return 'red'
      } else {
        return 'blue'
      }
    },
    formTitle: function () {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    }
  },
  methods: {
    submit () {
      this.$emit('onChange', this.webhook)
    },
    deleteWebhook () {
      this.$emit('onChange', null)
    },
    onFiltersChange (f) {
      this.webhook.filters = f.slice(0)
    },
    onHeadersChange (h) {
      this.webhook.headers = h.slice(0)
    },
    bodyChange (body) {
      this.webhook.body = body
    },
    toggleMethod () {
      if (this.webhook.method === 'GET') {
        this.webhook.method = 'POST'
      } else {
        this.webhook.method = 'GET'
      }
    }
  }
}
</script>
<style>
.GET {
  background-color: blue;
}
.POST {
  color:red;
}
.CodeMirror.cm-s-default {
  height: 120px;
}
</style>
