<template>
  <v-container fill-height justify-center  justify-start  fluid grid-list-sm>
    <v-layout  align-start wrap>
      <v-flex xs12>
        <v-card class="upy-card white">
          <v-card-title>
            {{application.name}} <br>
             update version {{application.__v}}
          </v-card-title>
          <v-card-text>
        <json-editor ref="editor" :onChange="onConfigChange" :json="configuration" />
      </v-card-text>
      <v-btn absolute fab bottom right color="primary" @click.native="saveApplication()">
          <v-icon>done</v-icon>
      </v-btn>
      </v-card>
    </v-flex>
  </v-layout>
</v-container>
</template>

<script>
import JSONEditor from 'vue2-jsoneditor'
import Events from '../../store/event-api.js'
import EventBus from '../../event-bus.js'
import Persistent from '../../store/persistent'

export default {
  name: 'AdvancedApplicationSettings',
  components: {
    'json-editor': JSONEditor
  },
  data: function () {
    return {
      application: {},
      configuration: null,
      output: null,
      mode: 'tree',
      version: null
    }
  },
  created () {
    this.loadApplication(this.$route.params.id)
  },
  mounted () {
    EventBus.$emit('set-title', 'Advanced application configuration')
    const editor = this.$refs.editor.editor
    editor.setMode(this.mode)
  },
  methods: {
    onConfigChange: function (c) {
      this.output = Object.assign({}, c)
    },
    saveApplication: function () {
      let url = Persistent.getters.baseUrl + '/api/v2/application/' + this.$route.params.id
      // prevent update if no changes
      if (JSON.stringify(this.output) === JSON.stringify(this.application.configuration)) {
        this.$router.push({
          name: 'ApplicationSettings',
          params: { id: this.$route.params.id }
        })
        return
      }
      // post data
      let data = Object.assign(this.application, {configuration: this.output})
      this.$http.post(url, data).then(function (res) {
        this.$router.push({
          name: 'ApplicationSettings',
          params: { id: this.$route.params.id }
        })
      }, response => {
        console.log(response)
        this.notifyError(response.status, response.body.message)
      })
    },
    loadApplication: function () {
      let url = Persistent.getters.baseUrl + '/api/v2/application/' + this.$route.params.id + ''
      let me = this
      this.$http.get(url).then(function (res) {
        if (res.data.application) {
          this.application = res.data.application
          EventBus.$emit('set-title', this.application.name + ' advanced configuration')
          if (res.data.application.configuration) {
            this.configuration = res.data.application.configuration
            this.output = res.data.application.configuration
            const editor = me.$refs.editor.editor
            setTimeout(function () {
              editor.expandAll()
            }, 1)
          } else {
            // this is a 500 error, server shall have served an clean new config
            this.configuration = {}
            this.notifyError(500, 'an empty configuration has been created')
          }
        }
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },
    notifyError: function (code, message) {
      console.log(Events.apiError, code, message)
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    }
  }
}
</script>
