.jsoneditor-frame {
  display: none;
}
.jsoneditor {
    border: 0!important;
}
.jsoneditor jsoneditor-mode-tree {
    border: 0!important;
}
jsoneditor-schema-error, div.jsoneditor td, div.jsoneditor textarea, div.jsoneditor th, div.jsoneditor-field, div.jsoneditor-value {
    font-family: 'Space Mono',monospace;
    font-size: 16pt;
    color: #000;
}
.jsoneditor-tree {
  background-color: white;
  padding-bottom: 135px;
}

.jsoneditor-menu {
  background-color: #F5F5F5!important;
}

.jsoneditor-contextmenu-root {
    position: relative;
    width: 0;
    height: 0;
}

.jsoneditor-outer {
    background: #424242;
}

div.jsoneditor-contextmenu ul li button {
    padding: 0;
    margin: 0;
    width: 124px;
    height: 24px;
    color: #4d4d4d;
    background: 0 0;
    font-size: 10pt;
    font-family: 'Space Mono',monospace;
}

</style>
