<template>
      <v-dialog width=640 v-model="dialog">
        <v-card class="upy-card">
          <v-card-title v-if="device.project && device.project._id">
            device is assigned to {{device.project.name}}
          </v-card-title>
          <v-card-title v-else>
            {{device.name}} has no project
          </v-card-title>
          <v-card-text>
            <p v-if="projects.length === 0" class="text-xs-center">
              You have no projects yet,<br>
              <v-btn @click="navigate('Projects')" center>Manage projects</v-btn>
            </p>
            <p v-if="device.project && device.project._id" class="text-xs-center">
              <v-btn
                @click="navigate('ProjectSettings', device.project._id )"
                center>Manage project {{device.project.name}}
              </v-btn>
            </p>
            <v-list subheader two-line v-if="projects.length > 0 && (!device.project || !device.project._id)">
            <v-subheader>please select an project below using the button</v-subheader>

                <template  v-for="(project, index) in projects">
                  <v-list-tile  :key="index + 'ws'"  class="mr-3">

                      <v-list-tile-content>
                          <v-list-tile-title>{{project.name}}</v-list-tile-title>
                          <v-list-tile-sub-title>{{project.slug}}</v-list-tile-sub-title>
                      </v-list-tile-content>

                      <v-list-tile-action>
                        <v-btn  class="info" @click.prevent="setProject(project._id)">
                        <v-icon>touch_app</v-icon>
                      </v-btn>
                      </v-list-tile-action>
                  </v-list-tile>
                  <v-divider :key="index" class="mr-3"></v-divider>
                </template>
            </v-list>
          </v-card-text>
          <v-card-actions>
            <v-btn v-if="device.project && device.project._id"
            flat @click.native="removeProject()"
            class="warning black--text">Remove Projects</v-btn>
            <v-spacer/>
            <v-btn  class="secondary" flat @click.native="dialog= false">
                Close
              </v-btn>
          </v-card-actions>
      </v-card>
          <v-confirm ref="confirm"></v-confirm>
        </v-dialog>
</template>

<script>
import Events from '../../store/event-api.js'
import EventBus from '../../event-bus.js'
import Persistent from '../../store/persistent'
import DialogConfirm from '@/components/global/DialogConfirm'

export default {
  name: 'DeviceProjects',
  props: ['dialogSignal'],
  data: function () {
    return {
      dialog: false,
      device: {},
      projects: []
    }
  },
  components: {
    'v-confirm': DialogConfirm
  },
  watch: {
    dialogSignal: function (val) {
      this.dialog = !this.dialog
    }
  },
  mounted () {
    this.loadDevice(this.$route.params.id)
    this.loadProjects()
  },
  methods: {
    navigate: function (component, id) {
      this.$router.push({
        name: component,
        params: {
          id: id
        }
      })
    },
    loadProjects: function () {
      let url = Persistent.getters.baseUrl + '/api/v2/projects'
      this.$http.get(url).then(function (res) {
        if (res.data && res.data.projects) {
          this.projects = res.data.projects
        }
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },
    loadDevice: function () {
      let url = Persistent.getters.baseUrl + '/api/v2/account/device/' + this.$route.params.id
      this.$http.get(url).then(function (res) {
        if (res.data && res.data.device) {
          this.device = res.data.device
          //
        }
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },
    setProject: async function (id) {
      if (!await this.$refs.confirm.open('Change project assigned', 'Do you confirm?', {
        color: 'info'
      })) {
        return
      }
      // POST /api/v2/application/APPLICATION_SLUG/:DEVICE_ID, assign device to application
      let url = Persistent.getters.baseUrl + '/api/v2/project/' + id + '/device/' + this.$route.params.id
      this.$http.post(url).then(function (res) {
        if (res.body && res.body.status === 200) {
          this.loadDevice()
          this.$emit('update')
        } else {
          this.notifyError(res.body.status, res.body.error)
        }
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },
    removeProject: async function () {
      if (!await this.$refs.confirm.open('Delete device-project assignement', 'Do you confirm?', {
        color: 'info'
      })) {
        return
      }
      let url = Persistent.getters.baseUrl + '/api/v2/account/device/' + this.$route.params.id + '/project'
      this.$http.delete(url).then(function (res) {
        this.loadDevice()
        this.$emit('update')
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },

    notifyError: function (code, message) {
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    }
  }
}
</script>
