<template>
  <v-container fill-height justify-center  justify-start  fluid grid-list-sm>
    <v-layout  align-start wrap>

      <v-flex xs12 md-6 offset-md-2>
        <v-card class="upy-card">
          <v-card-title>
            {{device.name}} credentials
          </v-card-title>
          <v-card-text>
            <v-list subheader two-line>
                <template  v-for="(credential, index) in credentials">
                  <v-divider :key="index" class="mr-3"></v-divider>
                  <v-list-tile :key="index + 'ws'"  class="mr-3">
                      <v-list-tile-content>
                          <v-list-tile-title>{{credential.access_token}}</v-list-tile-title>
                          <v-list-tile-sub-title>{{credential._id}}</v-list-tile-sub-title>
                      </v-list-tile-content>
                      <v-list-tile-action>
                        <v-btn  @click.native="deleteToken(credential._id)">
                        <v-icon class="warning--text">delete</v-icon>
                      </v-btn>
                      </v-list-tile-action>
                  </v-list-tile>
                </template>
            </v-list>

          </v-card-text>
          <v-card-actions>
            <v-btn flat primary @click.native="createToken()" class="primary black--text">Create token</v-btn>
          </v-card-actions>
      <v-btn absolute fab bottom right color="primary" @click.native="close()">
          <v-icon>done</v-icon>
      </v-btn>
      </v-card>

    </v-flex>

  </v-layout>
          <v-confirm ref="confirm"></v-confirm>
</v-container>
</template>

<script>
import Events from '../../store/event-api.js'
import EventBus from '../../event-bus.js'
import Persistent from '../../store/persistent'
import DialogConfirm from '@/components/global/DialogConfirm'

export default {
  name: 'DeviceCredentials',
  data: function () {
    return {
      device: {},
      credentials: []
    }
  },
  components: {
    'v-confirm': DialogConfirm
  },
  created () {
    this.loadCredentials(this.$route.params.id)
  },
  mounted () {
    EventBus.$emit('set-title', 'device credentials')
  },
  methods: {
    close: function () {
      this.$router.push({
        name: 'DeviceSettings',
        params: { id: this.device._id }
      })
    },
    createToken: function () {
      // /api/v2/account/device/:DEVICE_ID/credentials
      let url = Persistent.getters.baseUrl + '/api/v2/account/device/' + this.$route.params.id + '/credentials'
      let data = {} // Object.assign({}, this.configuration)
      this.$http.put(url, data).then(function (res) {
        this.loadCredentials(this.$route.params.id)
      }, response => {
        this.notifyError(response.status, response.body.message)
      })
    },
    loadCredentials: function () {
      // /api/v2/account/device/:DEVICE_ID/credentials
      let url = Persistent.getters.baseUrl + '/api/v2/account/device/' + this.$route.params.id + '/credentials'
      this.$http.get(url).then(function (res) {
        if (res.data && res.data.credentials && res.data.device) {
          this.device = res.data.device
          this.credentials = res.data.credentials
          EventBus.$emit('set-title', this.device.name + ' credentials')
        }
      }, response => {
        this.notifyError(response.status, response.statusText)
      })
    },
    deleteToken: async function (id) {
      if (await this.$refs.confirm.open('Delete this access token', 'Are you sure?', {
        color: 'warning'
      })) {
        // /api/v2/account/device/:DEVICE_ID/credentials
        let url = Persistent.getters.baseUrl + '/api/v2/account/device/' + this.$route.params.id + '/credential/' + id
        let data = {} // Object.assign({}, this.configuration)
        this.$http.delete(url, data).then(function (res) {
          this.loadCredentials(this.$route.params.id)
        }, response => {
          this.notifyError(response.status, response.body.message)
        })
      }
    },
    notifyError: function (code, message) {
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    }
  }
}
</script>
