<template>
  <v-toolbar v-if="isLoggedIn">
    <v-toolbar-side-icon  @click="toggleDrawer()"></v-toolbar-side-icon>
    <v-toolbar-title class="info--text">{{title}}</v-toolbar-title>
    <v-spacer></v-spacer>
  </v-toolbar>
</template>

<script>
import EventBus from '../event-bus.js'
import Events from '../store/event-api.js'
import Persistent from '../store/persistent'

export default {
  name: 'toolbar',
  props: ['isLoggedIn', 'bleOn', 'title'],

  methods: {
    logout: function () {
      Persistent.commit('deleteAuthToken')
      localStorage.clear()
      $cookies.set('token', null)
      window.location = '/'
    },

    notifyError: function (code, message) {
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    },
    toggleDrawer: function () {
      EventBus.$emit(Events.toggleDrawer)
    }
  }
}
</script>
