<template>
  <v-card class="upy-card" v-if="config && config.mqtt">
    <v-card-title>
      MQTT Settings
    </v-card-title>
    <v-card-text>
    <v-layout row wrap>

      <v-flex md3 xs6>
        <v-switch  v-model="config.enable_mqtt" label="Enable MQTT"></v-switch>
      </v-flex>

      <v-flex md3 xs6 v-if="config.mqtt_hook && config.enable_mqtt">
        <v-switch
          @change="onProjectDefault"
          v-model="config.mqtt_project_default"
          label="Project defaults">
      </v-switch>
      </v-flex>

      <v-flex md3 xs6 v-if="config.enable_mqtt">
        <v-switch
        @change="onPlatformDefault"
        v-model="config.mqtt_platform_default"
        label="Platform defaults"> {{ config.mqtt_platform_default }}
      </v-switch>
      </v-flex>
      <v-flex xs10 v-if="config.enable_mqtt">
        <v-text-field
          :disabled="onDefault"
          class="mr-4 ml-2"
          label="Server"
          @change="onChanged"
          v-model="config.mqtt.server">
      </v-text-field>
      </v-flex>
      <v-flex xs2 v-if="config.enable_mqtt">
        <v-text-field
          @change="onChanged"
          :disabled="onDefault"
          type="number"
          label="Port"
          v-model="config.mqtt.port" >
      </v-text-field>
      </v-flex>

      <v-flex md2 xs6  v-if="config.enable_mqtt && config.mqtt_platform_default">
        <v-switch
          @change="onChanged"
          v-model="config.store_messages"
          label="Store message">
        </v-switch>
      </v-flex>

      <v-flex md4 xs6
        v-if="config.enable_mqtt && config.store_messages && config.mqtt_platform_default">
          <v-slider step="1"
            tick
            class="pr-4"
            :label="config.retention_days  + ' days of storage'"

            v-model="config.retention_days"
            max="90"
            min="0" >
          </v-slider>
      </v-flex>

<!--
        <v-flex xs12 md2 ml-2 pr-4 v-if="mqttOn && config.mqtt && !config.mqtt_platform_default">
            <v-text-field :disabled="config.mqtt_platform_default" label="Client Hello" v-model="config.mqtt.client_id"></v-text-field>
        </v-flex>

          <v-flex xs12 md2 pr-5 v-if="mqttOn && config.mqtt && !config.mqtt_platform_default">
            <v-text-field  type="number" label="Keepalive" v-model="config.mqtt.keepalive"></v-text-field>
        </v-flex>
-->

<!--
        <v-flex md2 xs6  v-if="mqttOn  && !config.mqtt_platform_default">
          <v-switch  :disabled="config.mqtt_platform_default" v-model="config.mqtt.ssl" label="Enable TLS"></v-switch>
        </v-flex>

        <v-flex xs6 md4 v-if="mqttOn && !config.mqtt_platform_default && config.mqtt.ssl">
          <v-cert-upload  :disabled="config.mqtt_platform_default" kind="CA" :target="target" :linkTo="configuration"></v-cert-upload>
        </v-flex>
-->
      </v-layout>

</v-card-text>
  </v-card>
</template>
<script>
import Events from '../../store/event-api.js'
import EventBus from '../../event-bus.js'
import Persistent from '../../store/persistent'
import CertUpload from '../global/CertificateUpload'

export default {
  name: 'MQTT',
  props: ['configuration'],
  data () {
    return {
      config: { mqtt: {} },
      mqttOn: false,
      mqtt_platform_defaults: {}
    }
  },
  components: {
    'v-cert-upload': CertUpload
  },
  computed: {
    onDefault: function () {
      return this.config.mqtt_project_default ||
      this.config.mqtt_platform_default
    }
  },
  watch: {
    configuration (val) {
      this.config = val
    },
    days (val) {
      if (val) {
        this.config.retention_days = val
        this.onChanged()
      }
    }
  },
  mounted () {
    // init comp from props
    this.config = Object.assign({}, this.configuration)
    // grab the defaults
    const url = Persistent.getters.baseUrl + '/api/v2/applications/mqtt_platform_defaults'
    this.$http.get(url).then(function (res) {
      if (res.data && res.data.mqtt_platform_defaults) {
        this.mqtt_platform_defaults = res.data.mqtt_platform_defaults
      } else {
        this.notifyError(500, 'unable to get mqtt platform_defaultform defaults')
      }
    }, response => {
      this.notifyError(response.status, response.statusText)
    })
  },
  methods: {
    notifyError: function (code, message) {
      console.log(Events.apiError, code, message)
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    },
    onPlatformDefault: function (val) {
      if (!val) return
      console.log('onPlatformDefault!', val)
      if (this.config.mqtt_project_default) {
        this.config.mqtt_project_default = false
      }
      this.config.mqtt = Object.assign({}, this.mqtt_platform_defaults)
      this.config.store_messages = true
      if (!this.config.retention_days) {
        this.config.retention_days = 30
      }
      this.onChanged()
    },
    onProjectDefault: function (val) {
      if (this.config.mqtt_platform_default) {
        this.config.mqtt_platform_default = false
      }
      this.config.mqtt.server = this.config.mqtt_hook.AWS_CA.endpoint
      this.config.mqtt.port = 8883
      this.config.mqtt.ssl = true
      this.config.mqtt.ssl_params = { 'ca_file': '/flash/cert/aws-ca.pem' }
      this.config.store_messages = true
      if (!this.config.retention_days) {
        this.config.retention_days = 30
      }
      this.onChanged()
    },
    onChanged: function () {
      if (JSON.stringify(this.config) !== JSON.stringify(this.configuration)) {
        this.$emit('mqtt-edit', this.config)
      }
    }
  }
}

</script>
