import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'

Vue.use(Vuex)

export default new Vuex.Store({
  plugins: [createPersistedState()],
  state: {
    user: null,
    device: null,
    userID: null,
    authToken: null,
    baseUrl: null,
    serialPorts: [],
    serialPort: null,
    localFW: null
  },
  actions: {
    changeTheme (context) {
      context.commit('changeTheme')
    },
    changeAdvanced (context) {
      context.commit('changeAdvanced')
    }
  },
  mutations: {
    updateSerialPorts: (state, message) => {
      Vue.set(state, 'serialPorts', [...message])
    },
    setSerialPort: (state, message) => {
      Vue.set(state, 'serialPort', message)
    },
    setLocalFirmware: (state, message) => {
      Vue.set(state, 'localFW', message)
    },
    changeTheme: (state) => {
      state.user.darkTheme = !state.user.darkTheme
    },
    changeAdvanced: (state) => {
      state.user.advanced = !state.user.advanced
    },
    addAuthToken: (state, authToken) => {
      localStorage.setItem('authToken', authToken)
      state.authToken = authToken
    },
    deleteAuthToken: (state) => {
      state.authToken = null
    },
    setUser: (state, user) => {
      state.user = user
    },
    setDevice: (state, device) => {
      state.device = device
    },
    setBaseUrl: (state, url) => {
      state.baseUrl = url
    }
  },
  getters: {
    authToken: state => {
      return localStorage.getItem('authToken')
    },
    user: state => state.user,
    isDarkTheme: (state, getters) => {
      return getters.user ? getters.user.darkTheme : true
    },
    isAdvanced: (state, getters) => {
      return getters.user && getters.user.advanced
    },
    device: state => state.device,
    baseUrl: state => state.baseUrl,
    userEmail: state => state.userEmail,
    isLoggedIn: state => {
      return state.authToken !== null
    },
    getSerialPorts: state => {
      return state.serialPorts
    },
    getSerialPort: state => {
      return this.state.serialPort
    },
    localFW: state => state.localFW
  }
})
