<template>

    <v-layout width="120">
    <v-flex xs11>
      <label>Please select {{kind}} PEM file for {{target}} {{linkTo.name}}</label>
          <v-text-field max-width="120"></v-text-field>
        </v-flex>
    <v-flex xs1 text-xs-center>
            <upload-btn label="plop" icon primary :fileChangedCallback="pemUpload">
              <template slot="icon">
                <v-icon>cloud_upload</v-icon>
              </template>
            </upload-btn>
    </v-flex>
    </v-layout>

</template>

<script>
import Events from '../../store/event-api.js'
import EventBus from '../../event-bus.js'
import Persistent from '../../store/persistent'
import UploadButton from 'vuetify-upload-button'

export default {
  // link to is of type
  props: [
    'kind', // kind is of CA, CERT, KEY
    'linkTo', // application, device, or, project,
    'target' // the object (application, project or device )
  ],
  components: {
    'upload-btn': UploadButton
  },
  data: () => ({
    dialog: false
  }),
  methods: {
    pemUpload: function (file) {
      const url = Persistent.getters.baseUrl + '/api/v2/' + this.target + '/' + this.linkTo._id + '/' + this.target + '/certificate'
      console.log(url)
      let formData = new FormData()
      formData.append('file', file)
      this.$http.post(url, formData,
        {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }
      ).then(function () {
        this.$emit('onUpdate', true)
      })
        .catch(function (e) {
          this.notifyError(e.status, e.statusText)
        })
    },
    notifyError: function (code, message) {
      console.log(Events.apiError, code, message)
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    }
  }
}

</script>
