<template>
  <v-flex xs12>
    <v-data-table
      hide-headers
      hide-actions
      :headers="cols"
      :items="headers"
      class="elevation-1"
    >
      <template slot="items" slot-scope="props">
        <td class="text-xs-right">{{ props.item.key }}</td>
        <td class="text-xs-right">{{ props.item.value }}</td>
        <td class="justify-center layout px-0">
          <v-icon
            small
            class="mr-2"
            @click="editItem(props.item)"
          >
            edit
          </v-icon>
          <v-icon
            small
            @click="deleteItem(props.item)"
          >
            delete
          </v-icon>
        </td>
      </template>
      <template slot="footer">
        <v-btn v-if="headers.length === 0" @click="initialize">
            Reset
        </v-btn>
        <v-dialog v-model="dialog" max-width="500px">
          <v-btn slot="activator" class="mb-2">New HTTP Header</v-btn>
          <v-card class="upy-card">
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>
            <v-card-text>
              <v-container grid-list-md>
                <v-layout wrap>
                  <v-flex xs12 sm6 md4>
                    <v-text-field v-model="editedItem.key" label="Header"></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm6 md4>
                    <v-text-field v-model="editedItem.value" label="Value"></v-text-field>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" flat @click="close">Cancel</v-btn>
              <v-btn color="blue darken-1" flat @click="save">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
    </template>
  </v-data-table>
</v-flex>
</template>

<script>

const DEFAULT_HEADERS = [{key: 'Content-Type', value: 'application/json'}]

export default {
  props: ['headers'],
  name: 'HTTPHeaders',
  data () {
    return {
      dialog: false,
      cols: [
        {
          text: 'HTTP Header',
          sortable: false,
          align: 'left',
          value: 'key'
        },
        { text: 'value', align: 'left', value: 'value', sortable: false },
        { text: 'edit', align: 'left', value: 'edit', sortable: false }
      ],
      editedIndex: -1,
      editedItem: {
        key: '',
        value: ''
      },
      defaultItem: {
        key: '',
        value: ''
      },
      editorOptions: {lineNumber: false},
      content_types: ['application/json', 'application/octet-stream']
    }
  },
  computed: {
    formTitle: function () {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    }
  },
  methods: {
    initialize () {
      this.headers = DEFAULT_HEADERS.slice(0)
    },
    editItem (item) {
      this.editedIndex = this.headers.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },
    deleteItem (item) {
      const index = this.headers.indexOf(item)
      confirm('Are you sure you want to delete this item?') && this.headers.splice(index, 1)
      this.$emit('onChange', this.headers)
    },
    close () {
      this.dialog = false
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
        this.$emit('onChange', this.headers)
      }, 300)
    },
    save () {
      if (this.editedIndex > -1) {
        Object.assign(this.headers[this.editedIndex], this.editedItem)
      } else {
        this.headers.push(this.editedItem)
      }
      this.close()
    }
  }
}
</script>
