<template>
  <v-card class="upy-card flexcard" height="100%">
    <v-card-title class="applications">
      Application description
    </v-card-title>
    <v-card-text class="grow">

        <v-list two-line v-if="application.flash && application.flash.manifest">
          <v-list-group
            v-model="toggleContent"
            prepend-icon="folder"
            no-action
          >
            <v-list-tile slot="activator">
              <v-list-tile-content>
                <v-list-tile-title>/flash/</v-list-tile-title>
              </v-list-tile-content>
              <v-list-tile-action>
                <v-list-tile-action-text>{{ totalSize() }}</v-list-tile-action-text>
              </v-list-tile-action>
            </v-list-tile>

            <v-list-tile
              v-if="application.flash"
              v-for="f in application.flash.manifest"
              v-bind:key="f.path"
              @click="true">
              <v-list-tile-content>
                <v-list-tile-title class="caption ml-4">{{f.path}}</v-list-tile-title>
              </v-list-tile-content>

              <v-list-tile-action>
                <v-list-tile-action-text>{{fsize(f.size)}}</v-list-tile-action-text>
              </v-list-tile-action>
            </v-list-tile>
          </v-list-group>
        </v-list>

        <v-flex  xs12 v-if="application.flash && !application.flash.manifest">
          <p>You can upload a file or grab an Git URL with commit trigger enabled,
            in both case you must provide the flash directory in archive.</p>
          <v-form ref="form">
            <v-layout>
                <v-flex>
                <v-text-field
                style="margin-right: 96px; margin-bottom: 0px;"
                  clearable
                  outline
                  label="Sync folder in ZIP"
                  full-width
                  v-model="application.flash.sync_folder"
                ></v-text-field>
                  </v-flex>
            </v-layout>
            <v-layout row>
            <v-flex xs3>
            <upload-btn
              :disabled="!application.flash.sync_folder"
              class="uniqueId icon"
              :fileChangedCallback="uploadFlash">
              <template slot="icon">
                <v-icon class="white--text">cloud_upload</v-icon>
              </template>
            </upload-btn>
          </v-flex>
          <v-flex xs6>
            <v-text-field
              ref="zip_url"
              clearable
              outline

              label="https://..."
              :rules="zipUrlRules"
              v-model="application.flash.zip_url"
            ></v-text-field>
          </v-flex>
          <v-flex xs3>
            <v-btn
            class="primary"
            primary
            @click="grabZip"
            :disabled="!application.flash.sync_folder"
            >fetch url<v-icon>get_app</v-icon></v-btn>
          </v-flex>
                </v-layout>



          </v-form>

        </v-flex>
        <v-flex xs12 class="mt-2" v-if="application.flash && !application.flash.manifest && application.flash.sync_folder ">
          <v-form ref="form">

          </v-form>
        </v-flex>
        <span v-if="application.flash && application.flash.zip_url">
        <span >your flash is download from</span>
        <br>{{ application.flash.zip_url}}<br>
        <span class="white--text">your trigger url is POST</span>
        <br>{{ triggerUrl }}
      </span>

      <span v-if="application.flash">version {{ application.flash.__v}}</span>

    </v-card-text>
    <v-card-actions>
      <v-btn
      :disabled="!(application.flash && application.flash.manifest)"
      flat
      class="warning"
      @click="deleteFlash()">REMOVE</v-btn>

      <v-btn  flat class="primary" @click="saveFlash()">SAVE</v-btn>
      <v-btn v-if="application.flash && application.flash.zip_url" flat class="primary" @click="grabZip()">grab zip</v-btn>
      <!-- <v-btn flat class="info">Download</v-btn> -->
    </v-card-actions>
    <v-confirm ref="confirm"></v-confirm>
  </v-card>
</template>

<script>
import Persistent from '../../store/persistent'
import UploadButton from 'vuetify-upload-button'
import EventBus from '../../event-bus.js'
import Events from '../../store/event-api.js'
import filesize from 'filesize'
import DialogConfirm from '@/components/global/DialogConfirm'

export default {
  props: ['application'],
  components: {
    'v-confirm': DialogConfirm,
    'upload-btn': UploadButton
  },
  data: () => ({
    flash: null,
    toggleContent: false,
    zipUrl: null,
    syncFolderRules: [
      v =>
        /^(?!\/).\w.+[a-z]$/.test(
          v
        ) || 'Sync folder must not start or end by /'
    ],
    zipUrlRules: [
      v =>
        /https?:\/\/(www\.)?[-a-zA-Z0-9@:%._+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_+.~#?&//=]*)/.test(
          v
        ) || 'Url must be valid',
      v => (v && v.includes('.zip')) || 'Url must contains ".zip"'
    ]
  }),
  computed: {
    triggerUrl: function () {
      return window.location.origin + '/trigger/flash/zip/' + this.application.flash._id
    }
  },
  methods: {
    grabZip: function () {

      const applicationId = this.application._id
      let url =
        Persistent.getters.baseUrl +
        `/api/v2/application/${applicationId}/flash`
      let formData = new FormData()
      //         v-model="application.flash.zip_url"
      // formData.append("zip_url", this.application.flash.zip_url);
      formData.append('zip_url', this.application.flash.zip_url)
      formData.append('sync_folder', this.application.flash.sync_folder || '/')
      this.$http
        .put(url, formData)
        .then(function (resp) {
          if (resp.body.status !== 200) {
            this.notifyError(resp.body.status, resp.body.error)
            return
          }
          this.toggleContent = true
          var me = this
          setTimeout(function () {
            me.toggleContent = false
          }, 200)
          this.$emit('update')
        })
        .catch(function (e) {
          // window.location.reload();
          console.log(e)
          this.notifyError(e.status, e.statusText)
          this.$emit('update')
        })
    },
    totalSize: function () {
      const sizeModule = filesize.partial({ standard: 'iec' })
      if (!this.application ||
            !this.application.flash ||
            this.application.flash.manifest ||
            this.application.flash.manifest.length === 0) {
        return sizeModule(0)
      }
      const m = this.application.flash.manifest
      let t = 0
      for (let i = 0; i < m.length; i++) {
        if (m[i].size) {
          t = t + m[i].size
        }
      }
      try {
        return sizeModule(t)
      } catch (e) {
        console.log(t, e)
        return ''
      }
    },
    fsize: function (p) {
      if (!p) {
        return
      }
      const sizeModule = filesize.partial({ standard: 'iec' })
      try {
        return sizeModule(p)
      } catch (e) {
        console.log(p, e)
        return ''
      }
    },
    deleteFlash: async function () {
      if (
        await this.$refs.confirm.open(
          'Delete flash ',
          'Are you sure you want to delete flash application zip from the server ?',
          {
            color: 'warning'
          }
        )
      ) {
        const applicationId = this.application._id
        const url =
          Persistent.getters.baseUrl +
          `/api/v2/application/${applicationId}/flash`
        this.$http
          .delete(url)
          .then(function (err, res) {
            console.log(err, res)
            this.$emit('update')
          })
          .catch(function (e) {
            this.notifyError(e.status, e.statusText)
            this.$emit('update')
          })
      }
    },
    notifyError: function (code, message) {
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    },
    uploadFlash: function (file, b) {
      const applicationId = this.application._id
      let url =
        Persistent.getters.baseUrl +
        `/api/v2/application/${applicationId}/flash`
      let formData = new FormData()
      formData.append('file', file)
      formData.append('sync_folder', this.application.flash.sync_folder || '/')
      this.$http
        .put(url, formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        })
        .then(function (res) {
          if (res.body.status !== 200) {
            this.notifyError(res.body.status, res.body.error)
            return
          }
          this.toggleContent = true
          var me = this
          setTimeout(function () {
            me.toggleContent = false
          }, 200)
          this.$emit('update')
        })
        .catch(function (e) {
          window.location.reload()
          this.notifyError(e.status, e.statusText)
          this.$emit('update')
        })
    }
  }
}
</script>
<style>
.v-card-text {
}
.v-btn--disabled {
  background-color: rgba(0,0,0,0.12)!important;
  color: #B1B1B1!important;
}

</style>
