<template>

        <v-layout row wrap fill-height class="mb-0 pb-0">

          <v-flex class="ma-0 pa-0" xs12 v-if="showMore">
            <v-card  class="elevation-1 ma-0 pa-0">
              <v-layout row wrap pl-4 pr-4 text-xs-right>
                <v-flex xs12 md4 >
                  <v-select
                    v-model="selectedProject"
                    label="All projects"
                    :items="projects"
                    item-text="name"
                    item-value="_id"
                    clearable
                    class="mr-4">
                  </v-select>
                </v-flex>
                <v-flex xs12 md4>
                  <v-text-field append-icon="search"
                    v-model="deviceSearch"
                    class="mr-4"></v-text-field>
                </v-flex>
                <v-flex xs12 md4 pt-3 pr-4>
                <v-btn-toggle  v-model="toggle_mode">
                  <v-btn class="info" @click="selectMapView()" flat value="map">
                    <v-icon>map</v-icon>
                  </v-btn>
                  <v-btn class="info"
                    @click="selectDataTableView()"
                    flat
                    value="list">
                      <v-icon>view_list</v-icon>
                  </v-btn>
                  <v-btn class="info"
                    @click="selectCardView()"
                    flat
                    value="dashboard">
                      <v-icon>view_module</v-icon>
                  </v-btn>

                </v-btn-toggle>



              </v-flex>
            </v-layout>
          </v-card>
          </v-flex>




          <!--- data map view -->
          <v-layout v-if="mapView">

              <v-navigation-drawer
              permanent
              v-model="drawerRight"
              fixed
              right
              app>
              <v-list avatar three-line>
                  <template v-for="item in devices">
              <v-list-tile
                :key="item.title"
                avatar
                @click="navigate('DevicePath', item._id)"
              >
              <v-list-tile-action>
                <v-icon color="indigo">nfc</v-icon>
              </v-list-tile-action>
                <v-list-tile-content>
                  <v-list-tile-title >
                      {{ item.name }}
                  </v-list-tile-title>
                    <v-list-tile-sub-title v-html="item.unique_id"></v-list-tile-sub-title>

                </v-list-tile-content>
                <v-list-tile-action>
                  <v-btn icon ripple>
                    <v-icon color="grey lighten-1">info</v-icon>
                  </v-btn>
                </v-list-tile-action>

              </v-list-tile>
            </template>
              </v-list>
            </v-navigation-drawer>

          <v-flex>
              <v-card class=" ma-3">

                  <l-map
                    ref="mapref"
                    style="height: 505px; width: calc(100% + 60px);"
                    :zoom="zoom"
                  >
                    <l-tile-layer :url="url" :attribution="attribution"></l-tile-layer>
                    <l-marker v-if="device.location" v-bind:key='index'
                              v-for="(device, index) in filteredDevicesList"
                              :lat-lng="device.location.marker"
                              @click="navigate('DevicePath', device._id)" >
                              <l-tooltip>{{device.name}}</l-tooltip>
                    </l-marker>
                  </l-map>
              </v-card>
            </v-flex>
          </v-layout>
          <!--- data table view -->
          <v-layout pa-4 fill-height v-if="dataTableView">
            <v-flex xs12>
              <v-card>
              <v-data-table
                :items="filteredDevicesList"
                class="elevation-1"
                hide-actions
                hide-headers
              >
                <template v-slot:items="props">
                  <td>{{ props.item.name }}</td>
                  <td v-if="props.item.location">{{ props.item.location.country }}</td>
                  <td v-if="!props.item.location">Location Not Selected</td>
                  <td class="text-xs-right">
                    <v-btn small fab
                      class="ma-0 mt-1"
                      @click.stop="navigate('DeviceSettings', props.item._id)">
                      <v-icon color="dark--text">settings</v-icon>
                    </v-btn>
                    <v-btn fab small class="info" @click.stop="navigate('DeviceData', props.item._id)">
                      <v-icon>show_chart</v-icon>
                    </v-btn>
                  </td>
                </template>
              </v-data-table>
            </v-card>
            </v-flex>
          </v-layout>
          <v-layout pa-1 fill-height v-if="cardView">
            <v-flex  v-bind:key='index' v-for="(device, index) in filteredDevicesList"  mb-1 md4 sm6 xs12 pa-1>
            <v-card  v-bind:key="index" height="300" class="upy-card pa-2 elevation-4">
              <v-card-title class="pb-0 mb-0">
              <v-device-title :device='device'></v-device-title>
            </v-card-title>
            <v-card-text class="pt-0 mt-0">
              <!--
              <v-btn   v-if="device.project" class="ma-0 info"
                @click.stop="navigate('ProjectSettings', device.project._id)">
                  {{device.project.name}}
              </v-btn>
              -->
              <v-layout row class="fab-container">
                <v-btn
                  @click.stop="navigate('DeviceSettings', device._id)"
                  fab
                  small
                  class="device">
                  <v-icon>settings</v-icon>
                </v-btn>

                <v-btn  @click.stop="navigate('DeviceData', device._id)"
                  fab
                  small
                  class="info">
                  <v-icon>show_chart</v-icon>
                </v-btn>
              </v-layout>
                </v-card-text>
              </v-card>
            </v-flex>

          </v-layout>
          <span style="margin-top: -54px;">
          <v-btn small icon v-if="showMore" @click.stop="showMore = !showMore">
            <v-icon>close</v-icon>
          </v-btn>
          <v-btn small icon v-if="!showMore" @click.stop="showMore = !showMore">
            <v-icon>more_vert</v-icon>
          </v-btn>
        </span>

        </v-layout>

</template>

<script>

import EventBus from '../../event-bus.js'
import Events from '../../store/event-api.js'
import Persistent from '../../store/persistent'
import DeviceTitle from '../global/DeviceTitle.vue'
import L from 'leaflet'
import { LMap, LTileLayer, LMarker, LTooltip } from 'vue2-leaflet'

export default {
  name: 'Devices',
  props: ['config'],
  components: {
    'v-device-title': DeviceTitle,
    LMap,
    LTileLayer,
    LMarker,
    LTooltip
  },
  data () {
    return {
      drawerRight: false,
      showMore: true,
      toggle_mode: 'map',
      user: Persistent.getters.user,
      devices: [],
      projects: [],
      size: 'sm',
      query_search: null,
      deviceSearch: '',
      mapView: true,
      dataTableView: false,
      cardView: false,
      zoom: 1,
      url: 'http://{s}.tile.osm.org/{z}/{x}/{y}.png',
      attribution: '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',
      selectedProject: null
    }
  },
  created () {
    this.loadDevices()
    this.loadProjects()
  },
  mounted () {
    console.log('mounted ...')
    EventBus.$emit('set-title', 'devices list')
  },
  computed: {
    filteredDevicesList () {
      return this.devices.filter(device => {
        let selectedOne = true

        if (this.selectedProject) {
          selectedOne = device.project && device.project._id === this.selectedProject
        }

        if (this.deviceSearch) {
          let searchByName = device.name.toLowerCase().includes(this.deviceSearch.toLowerCase())

          if (this.selectedProject) {
            selectedOne = device.project && device.project._id === this.selectedProject && searchByName
          } else {
            selectedOne = searchByName
          }
        }
        return selectedOne
      })
    }
  },
  methods: {
    selectDataTableView: function () {
      this.dataTableView = true
      this.cardView = false
      this.mapView = false
    },
    selectCardView: function () {
      this.dataTableView = false
      this.mapView = false
      this.cardView = true
    },
    selectMapView: function () {
      this.dataTableView = false
      this.cardView = false
      this.mapView = true
      let _this = this
      this.$nextTick(function () {
        let latlngs = []
        _this.devices.forEach((device) => {
          if (device.location) {
            latlngs.push(device.location.marker)
          }
        })
        let polyline = L.polyline(latlngs)
        _this.$refs.mapref.fitBounds(polyline.getBounds())
      })
    },
    navigate: function (name, id) {
      this.$router.push({
        name,
        params: {
          id
        }
      })
    },
    addDevice: function () {
      let count = this.devices.length + 1
      let url = Persistent.getters.baseUrl + '/api/v2/account/devices'
      let data = {
        name: 'device ' + count
      }
      this.$http.put(url, data).then(function (res) {
        if (res.data.status === 200 && res.data.device._id) {
          this.$router.push({name: 'DeviceSettings', params: { id: res.data.device._id }})
        }
      }, response => {
        console.log(response)
        this.notifyError(response.status, response.body.message)
      })
    },
    loadDevices: function () {
      let url = Persistent.getters.baseUrl + '/api/v2/account/devices'
      this.$http.get(url).then(function (res) {
        if (res.body !== null) {
          this.devices = res.body.devices
          if (this.devices.length === 0) {
            this.$router.push({ name: 'DevicesClaim' })
          }
        }
      }, response => {
        console.log(response)
        this.notifyError(response.status, response.body.message)
      })
    },
    loadProjects: function () {
      let url = Persistent.getters.baseUrl + '/api/v2/projects'
      this.$http.get(url).then(function (res) {
        if (res.body.data !== null) {
          this.projects = res.body.projects
        }
      }, response => {
        console.log(response)
        this.notifyError(response.status, response.body.message)
      })
    },
    notifyError: function (code, message) {
      EventBus.$emit(Events.apiError, code, message)
      EventBus.$emit(Events.loadingEnd)
    }
  }

}

</script>
<style>
.fab-container {
  position: absolute;
  bottom: 0;
  right: 0;
}

.v-menu__content.menuable__content__active {
    z-index: 1000 !important;
}

</style>
